import React from 'react';
import axios from '../api/axios';

import Divider from '@material-ui/core/Divider';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';

import PageLoader from '../components/PageLoader';

class Payment extends React.Component {
	state = {
		payData: null
	}

	componentDidMount() {
		axios.get(`/api/tariff/${this.props.match.params.id}/`)
		.then(res => {
			this.setState({ payData: res.data })
		})
		.catch(err => {
			console.log(err.response);
		})
	}

	processPayment(){
		let payData = this.state.payData;
	  let widget = new window.cp.CloudPayments();
		let data = {
      bot_id: payData.bot_id
  	};
	  data.cloudPayments = {recurrent: { interval: 'Month', period: 1}};
	  widget.charge({
      publicId: payData.publicId,
      description: this.state.payData.description,
      amount: payData.amount,
      currency: 'RUB',
      invoiceId: payData.invoiceId,
      accountId: payData.accountId,
      data: data
	  },
	  function (options) {
			// success
	  },
	  function (reason, options) {
			// fail
	  });
	}

	onPayClick = () => {
		this.processPayment();
	}

	renderExpires(){
		if(this.state.payData.podpiska_do){
		 return (
			 <Typography variant="caption" gutterBottom>
				  : {this.state.payData.podpiska_do}
			 </Typography>
		 )
	 } else {
		 return null;
	 }
 }

	render() {
		if(!this.state.payData) return <PageLoader />;
		return (
			<div className="wrapper">
				<div className="row justify-content-center">

					<div className="col-xl-3 col-md-6">
						<Paper elevation={3} className="tariff">
							<div className="tariff-header">
								<Typography variant="h6" align="center">Free</Typography>
							</div>
							<div className="tariff-content">
								<ul>
									<li> </li>
								</ul>
							</div>
							<Divider />
							<div className="tariff-action">
								<Button disabled={true} variant="contained" size="large"></Button>
							</div>
						</Paper>
					</div>

					<div className="col-xl-3 col-md-6">
						<Paper elevation={3} className="tariff">
							<div className="tariff-header tariff-header--pro">
								<Typography variant="h6" align="center">Business</Typography>
							</div>
							<div className="tariff-content">
								<ul>
									<li> </li>
									<li></li>
									<li></li>
									<li></li>
								</ul>
							</div>
							<Divider />
							<div className="tariff-action">
								{this.renderExpires()}
								<Button
									color="primary"
									variant="contained"
									size="large"
									style={{ boxShadow: 'none' }}
									disabled={this.state.payData.podpiska_do ? true : false}
									onClick={this.onPayClick}
								>
									{this.state.payData.podpiska_do ? ' ' : `${this.state.payData.amount} RUB / `}
								</Button>
							</div>
						</Paper>
					</div>

				</div>
			</div>
		)
	}

}

export default Payment;
