import React, { Component } from 'react'
import * as Yup from 'yup';

import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import {Formik, Form} from 'formik'
import { Translate } from "react-localize-redux";

const validationSchema =  Yup.object().shape({
  pause: Yup.string()
  .required(<Translate id="validation.required" />),
});

export default class PauseEditor extends Component {
	handleClose = () => {
		this.props.editCard(null);
	}

	handleSubmit = () => {
		this.form.submitForm()
	}

	onSubmit = (values, actions) => {
		var data = {
			type: "pause",
			value: values.pause
		}
		this.props.editCard(data);
	}

	render() {
		var initialValues = {
			pause: ''
		};

		if (this.props.value !== null) {
			initialValues = {
				pause: this.props.value
			}
		}
		return (
			<Dialog open={true}>
				<DialogTitle><Translate id="editor.elements.pause" /></DialogTitle>
				<DialogContent>
					<Formik 
						onSubmit={this.onSubmit}
						initialValues={initialValues}
						validationSchema={validationSchema}
						ref={node => (this.form = node)}>
							{({ handleSubmit, handleChange, handleBlur, values, errors, touched }) => (
								<Form onSubmit={handleSubmit} style={{minWidth: '344px'}}>
									<TextField
										name="pause"
										type="number"
										value={values.pause}
										label={<Translate id="forms.pause" />}
										margin="normal"
										variant="outlined"
										onChange={handleChange}
										onBlur={handleBlur}
										error={touched.pause && Boolean(errors.pause)}
										helperText={errors.pause && touched.pause ? errors.pause : "" }
										required
										style={{width: "100%"}}
									/>
									<br/>
								</Form>
							)}
						</Formik>
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color="primary">
              <Translate id="btn.cancel" />
            </Button>
            <Button onClick={this.handleSubmit} color="primary">
              <Translate id="btn.save" />
            </Button>
          </DialogActions>
			</Dialog>
		)
	}
}
