import React, { Component } from 'react';

import Typography from '@material-ui/core/Typography';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import VideoIcon from '@material-ui/icons/MovieOutlined';
import VideoPlaceholder from '../../../assets/img/VideoPlaceholder.svg';


class Element extends Component {
	render() {
		if (this.props.data.text) {
			var descr = this.props.data.text;
			if (descr.length < 200) {
				descr = descr.substring(0, 200);
			} else {
				descr = descr.substring(0, 200) + '...';
			}
		}
		return (
			[<CardMedia
					key="cardMedia"
					image={VideoPlaceholder}
					style={{ paddingTop: '56.25%', height: '0' }}
			/>,
			<CardContent key="cardContent">
				<div className="card-type">
					<VideoIcon className="card-type_icon" style={{ fontSize: 16 }}/>
				</div>
				<Typography component="p" className="card-p">
					{descr}
				</Typography>
			</CardContent>]
		)
	}
}

export default Element;
