import React, { Component } from 'react';

import CardContent from '@material-ui/core/CardContent';
import File from '@material-ui/icons/AttachFile';
import Folder from '@material-ui/icons/Folder';
import { Translate } from "react-localize-redux";

class Element extends Component {
	render() {
		if (this.props.descr) {
			var descr = this.props.descr; 
			if (descr.length < 200) {
				descr = descr.substring(0, 200);
			} else {
				descr = descr.substring(0, 200) + '...';
			}
		}
		return (
			<CardContent style={{paddingBottom: 0}} key="cardContent">
				<div className="card-type">
					<File className="card-type_icon" style={{ fontSize: 16 }}/><Translate id="editor.elements.file" />
				</div>
				<a className="file-link" href={this.props.value} target="blank">
					<Folder style={{ fontSize: 48, marginRight: '10px' }} /> {this.props.value.match(/_(?:.(?!_))+$/g)[0].substring(1)}
				</a>
			</CardContent>
		)
	}
}

export default Element;