import React, { Component } from 'react'
import * as Yup from 'yup';

import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import {Formik, Form} from 'formik'
import { Translate } from "react-localize-redux";

const validationSchema =  Yup.object().shape({
  latitude: Yup.string()
  .required(<Translate id="validation.required" />),
  longitude: Yup.string()
  .required(<Translate id="validation.required" />),
  descr: Yup.string()
  .required(<Translate id="validation.required" />)
  .max(55, <Translate id="validation.max55" />)
});

export default class ContactEditor extends Component {
	handleClose = () => {
		this.props.editCard(null);
	}

	handleSubmit = () => {
		this.form.submitForm()
	}

	onSubmit = (values, actions) => {
		var data = {
			type: "geo",
			value: {
        latitude: values.latitude,
        longitude: values.longitude,
        descr: values.descr
      }
		}
		this.props.editCard(data);
	}

	render() {
		var initialValues = {
			latitude: '',
      longitude: '',
      descr: ''
		};

		if (this.props.value !== null) {
			initialValues = {
  			latitude: this.props.value.latitude,
        longitude: this.props.value.longitude,
        descr: this.props.value.descr
			}
		}
		return (
			<Dialog open={true}>
				<DialogTitle><Translate id="editor.elements.geo" /></DialogTitle>
				<DialogContent>
					<Formik
						onSubmit={this.onSubmit}
						initialValues={initialValues}
						validationSchema={validationSchema}
						ref={node => (this.form = node)}>
							{({ handleSubmit, handleChange, handleBlur, values, errors, touched }) => (
								<Form onSubmit={handleSubmit} style={{minWidth: '344px'}}>
									<TextField
										name="latitude"
										value={values.latitude}
										label={<Translate id="forms.latitude" />}
										margin="normal"
										variant="outlined"
										onChange={handleChange}
										onBlur={handleBlur}
										error={touched.latitude && Boolean(errors.latitude)}
										helperText={errors.latitude && touched.latitude ? errors.latitude : "" }
										required
										style={{width: "100%"}}
									/>
									<br/>
									<TextField
										name="longitude"
										value={values.longitude}
										label={<Translate id="forms.longitude" />}
										margin="normal"
										variant="outlined"
										onChange={handleChange}
										onBlur={handleBlur}
										error={touched.longitude && Boolean(errors.longitude)}
										helperText={errors.longitude && touched.longitude ? errors.longitude : "" }
										required
										style={{width: "100%"}}
									/>
                  <br/>
									<TextField
										name="descr"
                    required
										value={values.descr}
										multiline
										rowsMax={2}
										rows={2}
										label={<Translate id="forms.caption" />}
										margin="normal"
										variant="outlined"
										onChange={handleChange}
										onBlur={handleBlur}
										error={touched.descr && Boolean(errors.descr)}
										helperText={errors.descr && touched.descr ? errors.descr : "" }
										style={{width: "100%"}}
									/>
                  <Translate id="forms.geoTextHint" />
								</Form>
							)}
						</Formik>
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color="primary">
              <Translate id="btn.cancel" />
            </Button>
            <Button onClick={this.handleSubmit} color="primary">
              <Translate id="btn.save" />
            </Button>
          </DialogActions>
			</Dialog>
		)
	}
}
