class CreateCampaign extends React.Component {
	state = {
		open: false,
		fileName: '',
		tooLarge: false,
		serverError: false,
		submiting: false
	};

	handleClickOpen = () => {
		this.setState({ open: true });
	};

	handleClose = () => {
		this.setState({
			open: false,
			fileName: '',
			tooLarge: false,
			serverError: false
		});
	};

	handleSubmit = () => {
		this.form.submitForm();
	}

	onSubmit = (values, actions) => {
		this.setState({ submiting: true });
		var data = new FormData();
		if(values.file){
			let fileType = getFileType(values.file.type);
			data.append(fileType, values.file, values.file.name);
		}
		data.append('text', values.text);
		axios.post(`/api/compaign_list/${this.props.botId}/`, data)
		.then(res => {
			if (res.data.error) {
				this.setState({ serverError: res.data.error, submiting: false })
				actions.setSubmitting(false);
				return;
			}
			actions.setSubmitting(false);
			actions.resetForm({});
			this.props.submit(res.data);
			this.setState({ open: false, submiting: false, fileName: '', tooLarge: false, serverError: false });
		})
		.catch(err => {
			console.log(err);
			actions.setSubmitting(false);
			this.setState({ submiting: false, fileName: '', tooLarge: false, serverError: '   '});
		})
	}

	render() {
		var submiting = null;
		if (this.state.submiting === true){
			submiting = <div className="submiting" >
				<CircularProgress />
			</div>
		}
		return (
			<div style={{ marginBottom: '24px' }}>
				<Button onClick={this.handleClickOpen} variant="contained" color="primary">
					<AddIcon style={{ marginRight: 8 }} /> 
				</Button>
				<Dialog
					open={this.state.open}
					onClose={this.handleClose}
					aria-labelledby="form-dialog-title"
				>
					<DialogTitle id="form-dialog-title"> </DialogTitle>
					<DialogContent>
						{submiting}
						<Formik
							onSubmit={this.onSubmit}
							initialValues={{
								file: '',
								text: ''
							}}
							ref={node => (this.form = node)}>
								{({ handleSubmit, handleChange, handleBlur, values, errors, touched, setFieldValue }) => (
									<Form onSubmit={handleSubmit}>
										<label htmlFor="mailing-file" className="mailing-form_label">
											<Button variant="contained" component="span">
												<AttachmentIcon style={{ marginRight: 8 }} /> / 
											</Button>
										</label><br/>
										<input type="hidden" name="fileType" />
										{this.state.fileName ? this.state.fileName : null}
										<input
											name="file"
											type="file"
											id="mailing-file"
											onChange={(event) => {
												let fileName = event.currentTarget.files[0].name;
												let fileSize = ((event.currentTarget.files[0].size/1024)/1024).toFixed(4);
												let fileType = getFileType(event.currentTarget.files[0].type);
												this.setState({ serverError: false, tooLarge: false, fileName });
												if (fileType === 'photo' && fileSize <= 10) {
													setFieldValue('file', event.currentTarget.files[0]);
												} else if(fileType === 'video' && fileSize <= 50) {
													setFieldValue('file', event.currentTarget.files[0]);
												} else {
													setFieldValue('file', '');
													this.setState({ tooLarge: true });
												}
											}}
										/>
									{this.state.tooLarge ? <div className="field-error"> :  10MB,  50MB</div> : null}
										{this.state.serverError ? <div className="field-error">{this.state.serverError}</div> : null}
										<br/>
										<TextField
											name="text"
											value={values.text}
											multiline
											rowsMax={26}
											rows={6}
											label=""
											margin="normal"
											variant="filled"
											onChange={handleChange}
											onBlur={handleBlur}
											error={touched.text && Boolean(errors.text)}
											helperText={errors.text && touched.text ? errors.text : "" }
											style={{width: "355px"}}
										/>
									</Form>
								)}
							</Formik>

					</DialogContent>
					<DialogActions>
						<Button onClick={this.handleClose} color="primary">
							
						</Button>
						<Button onClick={this.handleSubmit} color="primary">
							
						</Button>
					</DialogActions>
				</Dialog>
			</div>
		);
	}
}


export default CreateCampaign;
