import React, { Component } from 'react'
import * as Yup from 'yup';

import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import {Formik, Form} from 'formik'
import { Translate } from "react-localize-redux";

const validationSchema =  Yup.object().shape({
  order: Yup.string()
  .required(<Translate id="validation.required" />),
  response: Yup.string()
  .required(<Translate id="validation.required" />),
});

export default class OrderEditor extends Component {
	handleClose = () => {
		this.props.editCard(null);
	}

	handleSubmit = () => {
		this.form.submitForm()
	}

	onSubmit = (values, actions) => {
		var data = {
			type: "order",
			value: {
				order: values.order, 
				response: values.response
			}
		}
		this.props.editCard(data);
	}

	render() {
		var initialValues = {
			order: '',
			response: ''
		};

		if (this.props.value !== null) {
			initialValues = {
				order: this.props.value.order,
				response: this.props.value.response
			}
		}
		return (
			<Dialog open={true}>
				<DialogTitle><Translate id="editor.elements.order" /></DialogTitle>
				<DialogContent>
					<Formik 
						onSubmit={this.onSubmit}
						initialValues={initialValues}
						validationSchema={validationSchema}
						ref={node => (this.form = node)}>
							{({ handleSubmit, handleChange, handleBlur, values, errors, touched }) => (
								<Form onSubmit={handleSubmit} style={{minWidth: '344px'}}>
									<TextField
										name="order"
										value={values.order}
										multiline
										rowsMax={26}
										rows={6}
										label={<Translate id="forms.order-text" />}
										margin="normal"
										variant="outlined"
										onChange={handleChange}
										onBlur={handleBlur}
										error={touched.order && Boolean(errors.order)}
										helperText={errors.order && touched.order ? errors.order : "" }
										required
										style={{width: "100%"}}
									/>
									<br/>
									<TextField
										name="response"
										value={values.response}
										multiline
										rowsMax={26}
										rows={6}
										label={<Translate id="forms.response" />}
										margin="normal"
										variant="outlined"
										onChange={handleChange}
										onBlur={handleBlur}
										error={touched.response && Boolean(errors.response)}
										helperText={errors.response && touched.response ? errors.response: "" }
										required
										style={{width: "100%"}}
									/>
								</Form>
							)}
						</Formik>
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color="primary">
              <Translate id="btn.cancel" />
            </Button>
            <Button onClick={this.handleSubmit} color="primary">
              <Translate id="btn.save" />
            </Button>
          </DialogActions>
			</Dialog>
		)
	}
}
