import React from 'react';
import { connect } from 'react-redux';
import * as actions from '../store/actions';
import * as Yup from 'yup';
import axios from 'axios';
import { Link } from 'react-router-dom';

import { Formik, Form } from 'formik'
import TextField from '@material-ui/core/TextField';
import CircularProgress from '@material-ui/core/CircularProgress';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import DoneOutlineIcon from '@material-ui/icons/DoneOutlineRounded';
import ReportIcon from '@material-ui/icons/Report';
import Zoom from '@material-ui/core/Zoom';



const validationSchema =  Yup.object().shape({
	pass: Yup.string()
  .min(8, '   8 ')
  .max(64, '   64 ')
	.required('  !'),
	repeatPass: Yup.string()
  .min(8, '   8 ')
  .max(64, '   64 ')
  .oneOf([Yup.ref('pass')], '  ')
	.required('  !')
});


class ChangePassword extends React.Component {
	state = {
		loading: true,
		finished: false,
		error: null,
		fieldError: null
	}

	componentDidMount(){
		let params = this.props.match.params;
		axios.get(`/api/ch_pass/${params.userId}/${params.queryId}/`)
		.then(res => {
			this.setState({ loading: false });
		})
		.catch((err) => {
			this.setState({
				loading: false,
				error: err.response.data.error
			});
		});
	}

	onSubmit = (values) => {
		let params = this.props.match.params;
		this.setState({ loading: true });
		axios.post(`/api/ch_pass/${params.userId}/${params.queryId}/`, {
			password: values.pass,
			password2: values.repeatPass
		})
		.then(res => {
			this.setState({
				loading: false,
				finished: true,
				fieldError: null
			});
		})
		.catch(err => {
			this.setState({
				loading: false,
				error: err.response.data.error
			});
		});
	}

	render() {
		return (
			<main className="auth-wrapper container">
				<header className="auth-header">
					<Link to="/" className="auth_logo">
  					<Typography align="center" color="primary" variant="h5" gutterBottom>INBOT24</Typography>
					</Link>
				</header>
				{!this.state.finished && !this.state.loading && !this.state.error ? <div className="auth-form">
					<Typography align="center" variant="h5" gutterBottom>
					   
					</Typography>
					<Formik
						onSubmit={(values) => this.onSubmit(values)}
						initialValues={{
							pass: '',
							repeatPass: ''
						}}
						validationSchema={validationSchema}
						ref={node => (this.form = node)}>
							{({ handleSubmit, handleChange, handleBlur, values, errors, touched }) => (
								<Form onSubmit={handleSubmit}>
									<TextField
										type="password"
										name="pass"
										label=" "
										margin="normal"
										variant="outlined"
										value={values.pass}
										onChange={handleChange}
										onBlur={handleBlur}
										error={touched.pass && Boolean(errors.pass)}
										helperText={errors.pass && touched.pass ? errors.pass : "" }
										required
										style={{minWidth: "300px"}}
									/>
									<br/>
									<TextField
										type="password"
										name="repeatPass"
										label=" "
										margin="normal"
										variant="outlined"
										value={values.repeatPass}
										onChange={handleChange}
										onBlur={handleBlur}
										error={touched.repeatPass && Boolean(errors.repeatPass)}
										helperText={errors.repeatPass && touched.repeatPass ? errors.repeatPass : "" }
										required
										style={{minWidth: "300px"}}
									/>
									<br/>
									{!this.state.loading && this.state.fieldError ? <div>
					  					<Typography className="auth_error" variant="body2" color="error" gutterBottom style={{ marginLeft: 12 }}>
												{this.state.fieldError}
					  					</Typography>
									</div> : null}
									<div className="text-center">
										<Button
											type="submit"
											variant="contained"
											color="primary"
											size="large"
											style={{ marginTop: 14 }}
										>
											
										</Button>
									</div>
								</Form>
							)}
						</Formik>
				</div> : null}
				{this.state.finished ? <div>
          <Zoom in={true} timeout={300}>
            <div className="text-center">
              <DoneOutlineIcon color="primary" style={{ fontSize: 64 }} />
            </div>
          </Zoom>
					<Typography align="center" variant="h5" gutterBottom> !</Typography>
					<div className="text-center">
						<Button
							to="/auth"
							color="primary"
							variant="outlined"
							component={Link}
						>
							
						</Button>
					</div>
				</div> : null}
				{this.state.loading ? <CircularProgress /> : null}
				{!this.state.loading && this.state.error ? <div>
	          <Zoom in={true} timeout={300}>
	            <div className="text-center">
	              <ReportIcon color="error" style={{ fontSize: 64 }} />
	            </div>
	          </Zoom>
  					<Typography align="center" variant="body1" color="error" gutterBottom>
							{this.state.error}
  					</Typography>
				</div> : null}
			</main>
		);
	}
}


const mapDispatchToProps = (dispatch) => {
	return {
		auth: (userData) => dispatch({ type: actions.SIGN_IN, userData }),
	}
}

export default connect(null, mapDispatchToProps)(ChangePassword);
