import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actions from '../../store/actions';

import Card from '@material-ui/core/Card';
// import CardActions from '@material-ui/core/CardActions';
import IconButton from '@material-ui/core/IconButton';

import EditIcon from '@material-ui/icons/EditOutlined';
// import DuplicateIcon from '@material-ui/icons/FileCopyOutlined';
import DeleteIcon from '@material-ui/icons/DeleteOutlined';

import CardEditor from './CardEditor';

// Card content
import TextContent from './cardContent/TextCard';
import ForkContent from './cardContent/ForkCard';
import MenuContent from './cardContent/MenuCard';
import InputContent from './cardContent/InputCard';
import RewindContent from './cardContent/RewindCard';
import ImageContent from './cardContent/ImageCard';
import VideoContent from './cardContent/VideoCard';
import FileContent from './cardContent/FileCard';
import OrderContent from './cardContent/OrderCard';
import PauseContent from './cardContent/PauseCard';
import ContactContent from './cardContent/ContactCard';
import GeoContent from './cardContent/GeoCard';

class Element extends Component {
	state = {
		editor: false
	}
	deleteCard = () => {
		this.props.removeElement(this.props.chainIndex, this.props.pageIndex, this.props.index);
	}

	editCard = (data) => {
		// this.props.enableEditorDrag();
		if (data === null) {
			this.setState({ editor: false });
			return;
		}
		this.props.editElement(this.props.chainIndex, this.props.pageIndex, this.props.index, data);
		this.setState({ editor: false });
	}

	handleEdit = () => {
		// this.props.disableEditorDrag();
		this.setState({ editor: true });
	}

	handleDuplicate = () => {
		// TODO Duplicate the card
		this.props.duplicateElement(this.props.chainIndex, this.props.pageIndex, this.props.index);
	}

	render() {
		const cardContent = () => {
			switch (this.props.type) {
				case 'text':
					return <TextContent data={this.props.data} />;
				case 'fork':
					return <ForkContent data={this.props.data} />;
				case 'menu':
					return <MenuContent data={this.props.data}  />;
				case 'input':
					return <InputContent data={this.props.data} />;
				case 'rewind':
					return <RewindContent data={this.props.data} />;
				case 'image':
					return <ImageContent data={this.props.data} botId={this.props.botId} />;
				case 'video':
					return <VideoContent data={this.props.data} botId={this.props.botId} />;
				case 'file':
					return <FileContent data={this.props.data} botId={this.props.botId} />;
				case 'order':
					return <OrderContent data={this.props.data} />;
				case 'pause':
					return <PauseContent data={this.props.data} />;
				case 'contact':
					return <ContactContent data={this.props.data} />;
				case 'geo':
					return <GeoContent data={this.props.data} />;
				default:
					return null;
			}
		}
		return (
			<Card
				elevation={1}
				className="card"
			>

				{cardContent()}
				<div className="card-small-actions">
					<IconButton
						size="small"
						aria-label="Edit"
						className="card-small_btn"
						onClick={this.handleEdit}
					>
						<EditIcon fontSize="inherit" />
					</IconButton>
					{/*
					<IconButton sizeSmall aria-label="Copy" onClick={this.handleDuplicate}>
						<DuplicateIcon fontSize="small" />
					</IconButton>
					*/}
					<IconButton
						size="small"
						aria-label="Delete"
						className="card_delete card-small_btn"
						onClick={this.deleteCard}
					>
						<DeleteIcon fontSize="inherit" />
					</IconButton>
				</div>

				{/*
				<CardActions disableActionSpacing className="card-actions" style={{ paddingTop: '0px' }}>
					<IconButton aria-label="Edit" onClick={this.handleEdit}>
						<EditIcon />
					</IconButton>
					<IconButton aria-label="Copy" onClick={this.handleDuplicate}>
						<DuplicateIcon />
					</IconButton>
					<IconButton aria-label="Delete" className="card_delete" onClick={this.deleteCard}>
						<DeleteIcon />
					</IconButton>
				</CardActions>
				*/}
				<CardEditor
					open={this.state.editor}
					editCard={this.editCard}
					type={this.props.type}
					data={this.props.data}
					botId={this.props.botId}
				/>
			</Card>

		)
	}
}

const mapDispatchToProps = (dispatch) => {
	return {
		removeElement: (chainIndex, pageIndex, index) => dispatch({ type: actions.REMOVE_ELEMENT, index, chainIndex, pageIndex }),
		editElement: (chainIndex, pageIndex, index, data) => dispatch({ type: actions.EDIT_ELEMENT, data, index, chainIndex, pageIndex }),
		duplicateElement: (chainIndex, pageIndex, index) => dispatch({ type: actions.DUPLICATE_ELEMENT, index, chainIndex, pageIndex }),
		disableEditorDrag: () => dispatch({ type: actions.DISABLE_EDITOR_DRAG }),
		enableEditorDrag: () => dispatch({ type: actions.ENABLE_EDITOR_DRAG })
	}
}

export default connect(null, mapDispatchToProps)(Element);
