import React, { Component } from 'react'
import * as Yup from 'yup';

import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import {Formik, Form, Field} from 'formik'
import { Translate } from "react-localize-redux";

const validationSchema =  Yup.object().shape({
  variable: Yup.string()
  .required(<Translate id="validation.required" />),
  variableType: Yup.string()
  .required(<Translate id="validation.required" />),
  desc: Yup.string()
  .required(<Translate id="validation.required" />),
  fallback: Yup.string()
  .required(<Translate id="validation.required" />)
});

export default class InputEditor extends Component {
	handleClose = () => {
		this.props.editCard(null);
	}

	handleSubmit = () => {
		this.form.submitForm()
	}

	onSubmit = (values, actions) => {
		var data = {
			type: "input",
			value: values.variable,
			variableType: values.variableType,
			fallback: values.fallback,
			descr: values.desc

		}
		this.props.editCard(data);
	}

	render() {
		var initialValues = {
			desc: '',
			variable: '',
			fallback: '',
			variableType: 'string'
		};

		if (this.props.value !== null) {
			console.log(this.props)
			initialValues = {
				desc: this.props.descr,
				variable: this.props.value,
				fallback: this.props.fallback,
				variableType: this.props.variableType
			}
		}
		return (
			<Dialog open={true}>
				<DialogTitle><Translate id="editor.elements.input" /></DialogTitle>
				<DialogContent>
					<Formik
						onSubmit={this.onSubmit}
						initialValues={initialValues}
						validationSchema={validationSchema}
						ref={node => (this.form = node)}>
							{({ handleSubmit, handleChange, handleBlur, values, errors, touched }) => (
								<Form onSubmit={handleSubmit} style={{minWidth: '344px'}}>
									<TextField
										name="desc"
										value={values.desc}
										multiline
										rowsMax={26}
										rows={6}
										label={<Translate id="forms.text" />}
										margin="normal"
										variant="outlined"
										onChange={handleChange}
										onBlur={handleBlur}
										error={touched.desc && Boolean(errors.desc)}
										helperText={errors.desc && touched.desc ? errors.desc : "" }
										required
										style={{width: "100%"}}
									/>
									<div style={{ display: "flex" }}>
										<TextField
											name="variable"
											value={values.variable}
											label={<Translate id="forms.variable-name" />}
											margin="normal"
											variant="outlined"
											onChange={handleChange}
											onBlur={handleBlur}
											error={touched.variable && Boolean(errors.variable)}
											helperText={errors.variable && touched.variable ? errors.variable : "" }
											style={{ marginTop: '16px', marginRight: '16px'}}
											required
										/>
										<Field name="variableType">{({ field, form }) => (
											<TextField
												{...field}
												select
												variant="outlined"
												label={<Translate id="forms.variable-type" />}
												error={touched.variableType && Boolean(errors.variableType)}
												helperText={errors.variableType && touched.variableType ? errors.variableType : "" }
												SelectProps={{
													native: true
												}}
												InputLabelProps={{
													shrink: true,
												}}
												style={{ marginTop: '16px', width: '150px'}}
											>
												<option value="string">String</option>
												<option value="number">Number</option>
												<option value="date">Date</option>
												<option value="email">Email</option>
												<option value="phone">Phone</option>
											</TextField>
										)}</Field>
									</div>
									<TextField
										name="fallback"
                    required
										value={values.fallback}
										label={<Translate id="forms.fallback" />}
										margin="normal"
										variant="outlined"
										onChange={handleChange}
										onBlur={handleBlur}
										style={{width: "100%", marginTop: '16px'}}
									/>
								</Form>
							)}
						</Formik>
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color="primary">
             	<Translate id="btn.cancel" />
            </Button>
            <Button onClick={this.handleSubmit} color="primary">
              <Translate id="btn.save" />
            </Button>
          </DialogActions>
			</Dialog>
		)
	}
}
