import React from 'react';
import { Link } from 'react-router-dom';
// import IconButton from '@material-ui/core/IconButton';
import BotIcon from '@material-ui/icons/Adb';
// import MoreVertIcon from '@material-ui/icons/MoreVertOutlined';
import Typography from '@material-ui/core/Typography';

class BotCard extends React.Component {
  render(){
    return (
			<Link
				className="list-card"
				key={this.props.id}
				to={`bot/${this.props.id}/editor`}
			>
				<div className={`list-card_icon ${this.props.active ? 'active' : ''}`}>
					<BotIcon style={{ fontSize: 48 }} />
				</div>
				<div className="list-card-content">
          <div className="list-card-content_left">
  					<Typography
              variant="subtitle2"
              color="secondary"
              style={{ textOverflow: 'ellipsis' }}>
              {this.props.name}
            </Typography>
  					<Typography variant="caption" color="secondary">
              {this.props.active ? '' : ''}
            </Typography>
          </div>
          <div className="list-card-content_right">
            {/*
            <IconButton onClick={() => console.log('1')} className="list-card_action">
              <MoreVertIcon />
            </IconButton>
            */}
          </div>
				</div>
			</Link>
    );
  }
}

export default BotCard;
