import React from 'react';
import IconButton from '@material-ui/core/IconButton';
import PrevIcon from '@material-ui/icons/NavigateBefore';
import NextIcon from '@material-ui/icons/NavigateNext';
import TableCell from '@material-ui/core/TableCell';
import LinearProgress from '@material-ui/core/LinearProgress';
import Typography from '@material-ui/core/Typography';

export default ({ currentPage, pages, onPageChange, loading }) => {
  if(pages < 2 || pages === 0) return null;
  return (
    <TableCell
      variant="footer"
      style={{ padding: '4px 8px' }}
      colSpan="4"
      className="table-pagination-cell"
    >
      {loading ? <LinearProgress className="table-pagination_loading" /> : null}
      <div className="table-pagination">
        <IconButton
          disabled={currentPage === 1 || loading}
          onClick={() => onPageChange(currentPage - 1)}
        >
          <PrevIcon />
        </IconButton>
        <Typography variant="caption" display="inline">{currentPage} / {pages}</Typography>
        <IconButton
          disabled={currentPage === pages || loading}
          onClick={() => onPageChange(currentPage + 1)}
        >
          <NextIcon />
        </IconButton>
      </div>
    </TableCell>
  );
}
