import React from 'react';
import { Link, Route } from 'react-router-dom';
import { connect } from 'react-redux';
import * as actions from '../store/actions';
import axios from '../api/axios';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Button from '@material-ui/core/Button';
import BackIcon from '@material-ui/icons/ArrowBack';
import Slide from '@material-ui/core/Slide';
import Zoom from '@material-ui/core/Zoom';
import SaveIcon from '@material-ui/icons/SaveOutlined';
import PlayIcon from '@material-ui/icons/PlayArrowOutlined';
import PauseIcon from '@material-ui/icons/PauseOutlined';

import Logo from '../assets/logo.png';
import BotConnection from '../components/BotConnection';

class EditorHeader extends React.Component {
  onSave = () => {
		axios.put(`/api/bot_data/${this.props.match.params.id}/`, this.props.editor, {
			crossDomain : true,
			withCredentials: true
		});
		this.props.saveEditorData();
  }

  onActiveToggle = () => {
		axios.put(`/api/bot_data/${this.props.match.params.id}/`, {
      is_active: !this.props.editor.is_active
    }, {
			crossDomain : true,
			withCredentials: true
		})
    .then(res => {
      this.props.toggleActive();
    });
  }

  renderSaveElements(){
    if(this.props.editorChanges){
      return (
        <React.Fragment>
          <Slide in={true} >
  					<div className="unsaved">
              <SaveIcon style={{ marginRight: 8 }} />
                
            </div>
  				</Slide>
					<Zoom in={true}>
            <Button
              variant="contained"
              style={{ boxShadow: 'none' }}
  						color="primary"
              onClick={this.onSave}
            >
              
            </Button>
          </Zoom>
        </React.Fragment>
      )
    } else {
      return null;
    }
  }

  renderActivityButton(){
    if(this.props.editor){
      return (
				<IconButton
          onClick={this.onActiveToggle}
          disabled={this.props.editor ? false : true}
          style={{ marginLeft: 8 }}
				>
          {this.props.editor.is_active ? <PauseIcon color="secondary" /> : <PlayIcon color="secondary" />}
				</IconButton>
      );
    } else {
      return null;
    }
  }

  renderControls(){
    return (
      <React.Fragment>
        {this.renderSaveElements()}
        <BotConnection botId={this.props.match.params.id} />
        {this.renderActivityButton()}
      </React.Fragment>
    )
  }

  render(){
    return (
			<AppBar position="static" className="header" color="inherit" elevation={0}>
				<Toolbar style={{ padding: '0px 20px'}}>
					<IconButton
						style={{ marginRight: '16px' }}
            className="button-link"
            component={Link}
            to="/"
					>
            <BackIcon color="secondary" />
					</IconButton>
          <div className="logo" style={{ flexGrow: 1 }}>
            <img src={Logo} alt="INBOT24" />
          </div>
          <Route exact path='/bot/:id/editor' render={() => this.renderControls()} />
				</Toolbar>
			</AppBar>
    );
  }
}

const mapStateToProps = (state) => {
	return {
		editor: state.editor,
		editorChanges: state.editorChanges
	}
}

const mapDispatchToProps = (dispatch) => {
	return {
		saveEditorData: () => dispatch({ type: actions.SAVE_EDITOR_DATA }),
		toggleActive: () => dispatch({ type: actions.TOGGLE_EDITOR_ACTIVE })
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(EditorHeader);
