import React, { Component } from 'react'
import TextEditor from '../cardEditor/TextEditor';
import ForkEditor from '../cardEditor/ForkEditor';
import MenuEditor from '../cardEditor/MenuEditor';
import InputEditor from '../cardEditor/InputEditor';
import RewindEditor from '../cardEditor/RewindEditor';
import ImageEditor from '../cardEditor/ImageEditor';
import FileEditor from '../cardEditor/FileEditor';
import OrderEditor from '../cardEditor/OrderEditor';
import PauseEditor from '../cardEditor/PauseEditor';
import ContactEditor from '../cardEditor/ContactEditor';
import GeoEditor from '../cardEditor/GeoEditor';
import VideoEditor from '../cardEditor/VideoEditor';

export default class CardEditor extends Component {
	editorType = () => {
		switch (this.props.type) {
			case 'text':
				return <TextEditor editCard={this.props.editCard} data={this.props.data}/>;
			case 'fork':
				return <ForkEditor editCard={this.props.editCard} data={this.props.data}/>;
			case 'menu':
				return <MenuEditor editCard={this.props.editCard} data={this.props.data}/>;
			case 'input':
				return <InputEditor editCard={this.props.editCard} data={this.props.data}/>;
			case 'rewind':
				return <RewindEditor editCard={this.props.editCard} data={this.props.data}/>;
			case 'image':
				return <ImageEditor editCard={this.props.editCard} data={this.props.data} botId={this.props.botId}/>;
			case 'video':
				return <VideoEditor editCard={this.props.editCard} data={this.props.data} botId={this.props.botId}/>;
			case 'file':
				return <FileEditor editCard={this.props.editCard} data={this.props.data} botId={this.props.botId}/>;
			case 'order':
				return <OrderEditor editCard={this.props.editCard} data={this.props.data} botId={this.props.botId}/>;
			case 'pause':
				return <PauseEditor editCard={this.props.editCard} data={this.props.data} botId={this.props.botId}/>;
			case 'contact':
				return <ContactEditor editCard={this.props.editCard} data={this.props.data}/>;
			case 'geo':
				return <GeoEditor editCard={this.props.editCard} data={this.props.data}/>;
			default:
				break;
		}
	}
	render() {
		return (
			<div>
				{this.props.open ? this.editorType() : null}
			</div>
		)
	}
}
