import React, { Component } from 'react'
import * as Yup from 'yup';
import axios from '../../api/axios';

import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import CircularProgress from '@material-ui/core/CircularProgress';
import LinearProgress from '@material-ui/core/LinearProgress';
import { Formik, Form } from 'formik';

const validationSchema = Yup.object().shape({
  video: Yup.mixed()
    .required('  '),
  text: Yup.string()
    .max(1024, ' 1024 ')
});

const validationSchemaEditing = Yup.object().shape({
  text: Yup.string()
    .max(1024, ' 1024 ')
});

export default class VideoEditor extends Component {
  state = {
    tooLarge: false,
    serverError: false,
    submiting: false,
		uploading: false,
		video: null
  }
	componentDidMount(){
    if(this.props.data !== null){
		    this.setState({ video: this.props.data.video });
    }
	}

  handleClose = () => {
    this.props.editCard(null);
  }

  handleSubmit = () => {
    this.form.submitForm()
  }

  onSubmit = (values, actions) => {
    this.setState({ submiting: true });
    var data = {
      type: "video",
      data: {
        video: this.state.video,
        text: values.text
      }
    }
    this.props.editCard(data);
  }

	handleImageUpload = (videoData) => {
    this.setState({ uploading: true });
    var data = new FormData();
    data.append('file', videoData);
    axios.post(`/api/upload_file/${this.props.botId}/`, data, {
      crossDomain: true,
      withCredentials: true
    }).then(res => {
      if (res.data.error) {
        this.setState({ serverError: res.data.error });
        return;
      }
      this.setState({ uploading: false, video: res.data.url });
    }).catch(err => {
      console.log(err);
      this.setState({ uploading: false });
      this.setState({ serverError: "[500]    .  " });
    });
	}

  render() {
    var initialValues = {
      text: ''
    };
    var submiting = null;

    if (this.props.data !== null) {
      initialValues = {
        text: this.props.data.text
      }
    }

    if (this.state.submiting === true) {
      submiting = <div className="submiting">
        <CircularProgress />
      </div>
    }
    return (<Dialog open={true}>
      <DialogTitle></DialogTitle>
      <DialogContent>
        {submiting}
        <Formik
          onSubmit={this.onSubmit}
          initialValues={initialValues}
          validationSchema={!this.props.data ? validationSchema : validationSchemaEditing}
          ref={node => (this.form = node)}
        >
          {
            ({ handleSubmit, handleChange, handleBlur, values, errors, touched, setFieldValue }) => (
							<Form
                onSubmit={handleSubmit}
                style={{ minWidth: '344px' }}
              >
              {/*
									{this.props.value === null ? <input id="image-uploader" name="image" type="file" accept="image/*" onChange={(event) => {
											this.setState({ serverError: false, tooLarge: false });
											if (((event.currentTarget.files[0].size/1024)/1024).toFixed(4) <= 1) {
												setFieldValue("image", event.currentTarget.files[0]);
											} else {
												setFieldValue("image", "");
												this.setState({ tooLarge: true });
											}

									}} />: null}
									{this.props.value === null ? <br/> : null}
									*/
              }
              <input id="video-upload-input" name="video" type="file" accept="video/*" onChange={(event) => {
                  this.setState({ serverError: false, tooLarge: false });
                  if(!event.currentTarget.files[0]) return;
                  if (((event.currentTarget.files[0].size / 1024) / 1024).toFixed(4) <= 50) {
                    setFieldValue("video", event.currentTarget.files[0]);
										this.handleImageUpload(event.currentTarget.files[0]);
                  } else {
                    setFieldValue("video", "");
                    this.setState({ tooLarge: true });
                  }
                }}/>
                {this.state.uploading
									? <LinearProgress color="secondary" />
                  : null
								}
                {/*
                <div className="video-upload">
									<label htmlFor="video-upload-input" >
		                {!this.state.uploading
											? <Button color="secondary" variant="outlined" component="span">
                        <CloudUploadIcon style={{ marginRight: 8 }} /> 
		                		</Button>
											: <CircularProgress color="secondary" />
										}
              		</label>
								</div>
                */}
								<br/>
              {
                this.state.serverError
                  ? <div className="field-error">{this.state.serverError}</div>
                  : null
              }
              {
                this.state.tooLarge
                  ? <div className="field-error">   50MB</div>
                  : null
              }
              <TextField
                name="text"
                value={values.text}
                multiline
                rowsMax={16}
                rows={6}
                label=''
                margin="normal"
                variant="filled"
                onChange={handleChange}
                onBlur={handleBlur}
                error={touched.text && Boolean(errors.text)}
                helperText={errors.text && touched.text ? errors.text : ""}
                style={{
                  width: "100%"
                }}/>
              <br/>
            </Form>)
          }
        </Formik>
      </DialogContent>
      <DialogActions>
        <Button onClick={this.handleClose} color="primary">
          
        </Button>
        <Button onClick={this.handleSubmit} color="primary" disabled={this.state.uploading}>
          
        </Button>
      </DialogActions>
    </Dialog>)
  }
}
