import React from 'react';
import { connect } from 'react-redux';
import * as actions from '../store/actions';
import * as Yup from 'yup';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Typography from '@material-ui/core/Typography';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Formik, Form } from 'formik'
import axios from '../api/axios';

const validationSchema =  Yup.object().shape({
  token: Yup.string()
  .required('  ')
});

class BotConnection extends React.Component {
  state = {
    open: false,
    submiting: false,
    error: false
  }

  onConnectClick = () => {
    this.setState({ open: true });
  }

  onClose = () => {
    this.setState({ open: false });
  }

	handleSubmit = () => {
		this.form.submitForm()
	}

  onSubmit = (values) => {
    this.setState({ submiting: true, error: false  });
    axios.post(`/api/set_webhook/${this.props.botId}/`, {
      token: values.token
    })
    .then(res => {
      this.props.connectBot();
      this.setState({ submiting: false, open: false, error: false })
    })
    .catch(err => {
      this.setState({
        error: '  ',
        submiting: false
      });
    })
  }

  renderSubmitingLoader() {
		if (this.state.submiting === true){
			return (<div className="submiting" >
				<CircularProgress />
			</div>);
		} else return null;
  }

  render() {
    if(!this.props.editor) return null;
    let status = this.props.editor.status === 'on' ? true : false;
    return (
      <React.Fragment>
        <Button
          style={{ marginLeft: 8 }}
          color="secondary"
          onClick={this.onConnectClick}
        >
          {status ? ' ' : ''}
        </Button>
  			<Dialog open={this.state.open}>
  				<DialogTitle> </DialogTitle>
  				<DialogContent>
            {this.renderSubmitingLoader()}
            {this.state.error ? (
              <Typography variant="caption" color="error" display="block" gutterBottom>
                {this.state.error}
              </Typography>
            ) : null}
  					<Formik
  						onSubmit={this.onSubmit}
  						initialValues={{ token: '' }}
  						validationSchema={validationSchema}
  						ref={node => (this.form = node)}>
  							{({ handleSubmit, handleChange, handleBlur, values, errors, touched }) => (
  								<Form onSubmit={handleSubmit} style={{minWidth: '344px'}}>
  									<TextField
  										name="token"
  										value={values.token}
  										label=""
  										margin="normal"
  										variant="filled"
  										onChange={handleChange}
  										onBlur={handleBlur}
  										error={touched.token && Boolean(errors.token)}
  										helperText={errors.token && touched.token ? errors.token : "" }
  										required
  										style={{width: "100%"}}
  									/>
  									<br/>
  								</Form>
  							)}
  						</Formik>
            </DialogContent>
            <DialogActions>
              <Button onClick={this.onClose} color="primary">
                
              </Button>
              <Button onClick={this.handleSubmit} color="primary">
                
              </Button>
            </DialogActions>
  			</Dialog>
      </React.Fragment>
    );
  }
}

const mapStateToProps = (state) => {
	return {
		editor: state.editor
	}
}

const mapDispatchToProps = (dispatch) => {
	return {
		connectBot: () => dispatch({ type: actions.CONNECT_BOT })
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(BotConnection);
