import React, { Component } from 'react';
import { connect } from 'react-redux';

import Typography from '@material-ui/core/Typography';
import CardContent from '@material-ui/core/CardContent';
import FastForward from '@material-ui/icons/FastForward';
import getPages from '../../../helpers/getPages';
import { Translate } from "react-localize-redux";

class Element extends Component {
	render() {
		var pages = getPages(this.props.editor);
		return (
			<CardContent style={{paddingBottom: 0}}>
				<div className="card-type">
					<FastForward className="card-type_icon" style={{ fontSize: 16 }}/><Translate id="editor.elements.rewind" />
				</div>
				<Typography component="p" className="card-p">
					{pages.find(page => page.id === this.props.value) ? pages.find(page => page.id === this.props.value).name : ''}
				</Typography>
			</CardContent>
		)
	}
}

const mapStateToProps = state => {
	return {
		editor: state.editor
	}
}

export default connect(mapStateToProps)(Element);