import React, { Component } from 'react'
import axios from '../api/axios';
import { connect } from 'react-redux';
import * as actions from '../store/actions';

import CircularProgress from '@material-ui/core/CircularProgress';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import ExitIcon from '@material-ui/icons/ExitToAppOutlined';

import Logo from '../assets/logo.png';
import CreateBot from '../components/CreateBot';
import BotCard from '../components/BotCard';


class Bots extends Component {
	state = {
		loader: true,
		dialog: false,
		bots: null
	}
	componentDidMount = () => {
		this.props.clearEditorData();
		axios.get('api/bot_list/', {
			crossDomain : true,
			withCredentials: true
		})
		.then(res => {
			if (res.data.error) {
				console.log(res.data.error);
			}
			var bots = res.data;
			this.setState({loader: false, bots: bots});
		})
		.catch(err => {
			console.log(err);
		})
	}

	handleBotAdd = (bot) => {
		var bots = [...this.state.bots];
		bots.push(bot);
		this.setState({ bots });
	}

  editBot = (id, name) => {
		let bots = [...this.state.bots];
		let botIndex = bots.findIndex(bot => bot.id === id);
		bots[botIndex].name = name;
		this.setState({
			bots: bots
		})
  }

	renderBotList(){
		if (this.state.bots === null) {
			return (
				<div className="col-12">
					<div className="text-center"><CircularProgress style={{marginTop: '24px'}} /></div>
				</div>
			);
		} else {
			return this.state.bots.map((el) => (
				<div className="col-12 xol-md-6 col-lg-3 xol-xl-3" key={el.id}>
					<BotCard
						id={el.id}
						name={el.name}
						active={el.is_active}
					/>
				</div>
			));
		}
	}


	render() {
		return (
			<main className="list-main">
				<AppBar position="static" className="header" color="inherit" elevation={0}>
					<Toolbar>
	          <div className="logo" style={{ flexGrow: 1 }}>
	            <img src={Logo} alt="INBOT24" />
	          </div>
						<IconButton
							style={{ marginLeft: '16px' }}
							onClick={this.props.logout}
						>
							<ExitIcon />
						</IconButton>
					</Toolbar>
				</AppBar>

				<div className="container list-container">
					<div className="row">
						{this.renderBotList()}
						{this.state.bots ? <CreateBot addBot={this.handleBotAdd} /> : null}
					</div>
				</div>

			</main>
		)
	}
}

const mapDispatchToProps = (dispatch) => {
	return {
		clearEditorData: () => dispatch({ type: actions.CLEAR_EDITOR_DATA }),
		logout: () => dispatch({ type: actions.LOGOUT })
	}
}

export default connect(null, mapDispatchToProps)(Bots);
