import * as cloneDeep from 'lodash/cloneDeep';
import uuid from 'uuid/v4';

import * as actions from './actions';
import { loadState } from '../helpers/localStorage';

const localState = loadState();
const isSignedIn = localState && localState.refreshExpires > new Date().getTime() ? true : false
const initialState = {
	editor: null,
	editorChanges: false,
	// editorDragDisabled: false,
	// analytics: {
	// 	numbers: null,
	// 	charts: null,
  //   fromDate: new Date(Date.now() - 1000*60*60*24*30),
  //   toDate: new Date(),
	// 	period: "day"
	// },
	// settings: null,
	auth: {
		isSignedIn: isSignedIn,
		token: localState && isSignedIn ? localState.token : null,
		refresh: localState && isSignedIn ? localState.refresh : null,
		expires: localState && isSignedIn ? localState.expires : null,
		refreshExpires: localState && isSignedIn ? localState.refreshExpires : null
	}
}

export default ( state = initialState, action ) => {
	switch ( action.type ) {
		case actions.SIGN_IN: {
			let expires = new Date().getTime() + 300000;
			let refreshExpires = new Date().getTime() + (60 * 60 * 24 * 1000 * 13);
			return {
				...state,
				auth: {
					isSignedIn: true,
					token: action.payload.access,
					refresh: action.payload.refresh,
					expires: expires,
					refreshExpires: refreshExpires
				}
			}
		}
		case actions.REFRESH_TOKEN:
			return {
				...state,
				auth: {
					...state.auth,
					expires: new Date(new Date().getTime() + 300000).getTime(),
					token: action.userData.token,
				}
			}
		case actions.LOGOUT:
			return {
				...state,
				editor: null,
				editorChanges: false,
				editorDragDisabled: false,
				analytics: {
					numbers: null,
					charts: null,
			    fromDate: new Date(Date.now() - 1000*60*60*24*30),
			    toDate: new Date(),
					period: "day"
				},
				settings: null,
				auth: {
					isSignedIn: false,
					expires: null,
					token: null,
					refresh: null,
					refreshExpires: null
				}
			}
		case actions.LOAD_EDITOR_DATA:
			return {
				...state,
				editor: action.editor,
				editorChanges: false
			}
		case actions.TOGGLE_EDITOR_ACTIVE:
			return {
				...state,
				editor: {
					...state.editor,
					is_active: !state.editor.is_active
				},
				editorChanges: false
			}
		case actions.SAVE_EDITOR_DATA:
			return {
				...state,
				editorChanges: false
			}
		case actions.LOAD_ANALYTICS_DATA:
			return {
				...state,
				analytics: action.analytics
			}
		case actions.LOAD_SETTINGS_DATA:
			return {
				...state,
				settings: action.settings
			}
		case actions.LOAD_FAQ_DATA:
			return {
				...state,
				faq: action.faq
			}
		case actions.CLEAR_EDITOR_DATA:
			return {
				...state,
				editor: null,
				editorChanges: false,
				analytics: {
					numbers: null,
					charts: null,
			    fromDate: new Date(Date.now() - 1000*60*60*24*30),
			    toDate: new Date(),
					period: "day"
				},
				settings: null
			}
		// CHAIN
		case actions.ADD_CHAIN:
			return {
				...state,
				editor: {
					...state.editor,
					data: state.editor.data.concat( [[]] )
				},
				editorChanges: true
			}

		// PAGE
		case actions.ADD_PAGE: {
			let editorData = cloneDeep(state.editor.data);
			editorData[action.chainIndex].push({
				name: action.pageName,
				id: uuid(),
				elements: []
			});
			return {
				...state,
				editor: {
					...state.editor,
					data: editorData
				},
				editorChanges: true
			}
		}
		case actions.EDIT_PAGE_TITLE: {
			let editorData = cloneDeep(state.editor.data);
			editorData[action.chainIndex][action.pageIndex].name = action.newTitle;
			return {
				...state,
				editor: {
					...state.editor,
					data: editorData
				},
				editorChanges: true
			}
		}
		case actions.DUPLICATE_PAGE: {
			let editorData = cloneDeep(state.editor.data);
			let copy = {...editorData[action.chainIndex][action.pageIndex]};
			copy.id = uuid();
			copy.elements = copy.elements.map(element => {
				element.id = uuid();
				return element;
			});
	  	editorData[action.chainIndex].splice(action.pageIndex, 0, copy);
			return {
				...state,
				editor: {
					...state.editor,
					data: editorData
				},
				editorChanges: true
			}
		}
		case actions.REMOVE_PAGE: {
			let editorData = cloneDeep(state.editor.data);
			editorData[action.chainIndex].splice(action.pageIndex, 1);
			return {
				...state,
				editor: {
					...state.editor,
					data: editorData
				},
				editorChanges: true
			}
		}

		// ELEMENT
		case actions.ADD_ELEMENT: {
			let editorData = cloneDeep(state.editor.data);
			let elements = editorData[action.chainIndex][action.pageIndex].elements;
			let element = action.data;
			element.id = uuid();
			elements.push(element);
			return {
				...state,
				editor: {
					...state.editor,
					data: editorData
				},
				editorChanges: true
			}
		}
		case actions.REMOVE_ELEMENT: {
			let editorData = cloneDeep(state.editor.data);
			editorData[action.chainIndex][action.pageIndex].elements.splice(action.index, 1);
			return {
				...state,
				editor: {
					...state.editor,
					data: editorData
				},
				editorChanges: true
			}
		}
		case actions.DISABLE_EDITOR_DRAG: {
			return {
				...state,
				editorDragDisabled: true
			}
		}
		case actions.ENABLE_EDITOR_DRAG: {
			return {
				...state,
				editorDragDisabled: false
			}
		}
		case actions.MOVE_ELEMENT: {
			let dest = action.destination;
			let source = action.source;

	    if(!dest || (dest.droppableId === source.droppableId && dest.index === source.index)){
	      return state;
	    }
			let editorData = cloneDeep(state.editor.data);

			if(action.draggableId.split(':')[0] === 'page'){
				// Move page
				let sourceChainIndex = action.draggableId.split(':')[1];
				let	sourcePageIndex = action.draggableId.split(':')[2];
				let destChainIndex = dest.droppableId.split(':')[0];
				let page = cloneDeep(editorData[sourceChainIndex][sourcePageIndex]);
				editorData[sourceChainIndex].splice(source.index, 1);
		  	editorData[destChainIndex].splice(dest.index, 0, page);
				return {
					...state,
					editor: {
						...state.editor,
						data: editorData
					},
					editorChanges: true
				};

			} else {
				// Move element
				let sourceChainIndex = action.draggableId.split(':')[2];
				let	sourcePageIndex = action.draggableId.split(':')[3];
				let destChainIndex = dest.droppableId.split(':')[1];
				let destPageIndex = dest.droppableId.split(':')[2];
				let element = cloneDeep(editorData[sourceChainIndex][sourcePageIndex].elements[source.index]);
				editorData[sourceChainIndex][sourcePageIndex].elements.splice(source.index, 1);
		  	editorData[destChainIndex][destPageIndex].elements.splice(dest.index, 0, element);
				return {
					...state,
					editor: {
						...state.editor,
						data: editorData
					},
					editorChanges: true
				};
			}
		}
		case actions.EDIT_ELEMENT: {
			let editorData = cloneDeep(state.editor.data);
			editorData[action.chainIndex][action.pageIndex].elements[action.index].data = action.data.data;
			return {
				...state,
				editor: {
					...state.editor,
					data: editorData
				},
				editorChanges: true
			}
		}
		case actions.DUPLICATE_ELEMENT: {
			let editorData = cloneDeep(state.editor.data);
			let copy = {...editorData[action.chainIndex][action.pageIndex].elements[action.index]};
			copy.data = cloneDeep(copy.data);
			copy.id = uuid();
	  	editorData[action.chainIndex][action.pageIndex].elements.splice(action.index, 0, copy);
			return {
				...state,
				editor: {
					...state.editor,
					data: editorData
				},
				editorChanges: true
			}
		}
		case actions.CONNECT_BOT: {
			return {
				...state,
				editor: {
					...state.editor,
					status: 'on'
				}
			}
		}

		default: return state;
	}
}
