import React from 'react';
import axios from '../api/axios';
import * as Yup from 'yup';

import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import { Formik, Form } from 'formik'
import AddIcon from '@material-ui/icons/AddOutlined'

const validationSchema =  Yup.object().shape({
	botName: Yup.string()
	.required('  ')
});


export default class CreateBot extends React.Component {
	state = {
		open: false,
	};

	handleClickOpen = () => {
		this.setState({ open: true });
	};

	handleClose = () => {
		this.setState({ open: false });
	};

	handleSubmit = () => {
		this.form.submitForm();
	}

	onSubmit = (values, actions) => {
		axios.post('api/bot_list/', {
			name: values.botName
		}, {
			crossDomain: true,
			withCredentials: true
		})
		.then(res => {
			if (res.data.error) {return}
			this.props.addBot(res.data);
			this.handleClose();
		})
		.catch(err => {
			console.log(err);
			actions.setSubmitting(false);
		});
	}

	render() {
		return (
			<div className="col-12 xol-md-6 col-lg-3 xol-xl-3">
				<Button
					onClick={this.handleClickOpen}
					className="list_new"
					variant="outlined"
					color="primary">
					<AddIcon style={{ marginRight: 4 }} /> 
				</Button>
				<Dialog
					open={this.state.open}
					onClose={this.handleClose}
					aria-labelledby="form-dialog-title"
				>
					<DialogTitle id="form-dialog-title"> </DialogTitle>
					<DialogContent>
						<Formik
							onSubmit={this.onSubmit}
							initialValues={{ botName: '' }}
							validationSchema={validationSchema}
							ref={node => (this.form = node)}>
								{({ handleSubmit, handleChange, handleBlur, values, errors, touched }) => (
									<Form onSubmit={handleSubmit}>
										<TextField
											type="text"
											name="botName"
											id="botName"
											label=""
											margin="normal"
											variant="filled"
											onChange={handleChange}
											onBlur={handleBlur}
											error={touched.botName && Boolean(errors.botName)}
											helperText={errors.botName && touched.botName ? errors.botName : "" }
											required
											style={{minWidth: "300px"}}
										/>
									</Form>
								)}
							</Formik>
					</DialogContent>
					<DialogActions>
						<Button onClick={this.handleClose} color="primary">
							
						</Button>
						<Button onClick={this.handleSubmit} color="primary">
							
						</Button>
					</DialogActions>
				</Dialog>
			</div>
		);
	}
}
