import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';

import Sidebar from './components/Sidebar';
import EditorHeader from './components/EditorHeader';
import Bots from './pages/Bots';
import Editor from './pages/Editor';
import Clients from './pages/Clients';
import Mailing from './pages/Mailing';
import Analytics from './pages/Analytics';
import Payment from './pages/Payment';
// import Orders from './pages/Orders';
// import Future from './pages/Future';
// import Connect from './pages/Connect';
// import Integration from './pages/Integration';
// import Settings from './pages/Settings';
// import FAQ from './pages/FAQ';
import Auth from './pages/Auth';
import ChangePassword from './pages/ChangePassword';
import ResetPassword from './pages/ResetPassword';

class App extends Component {
  renderAuthenticatedRoutes(){
    return (
      <React.Fragment>
        <div>
          <Route exact path='/' component={Bots} />
          <Switch>
            <Route path="/bot/:id" component={EditorHeader} />
          </Switch>
          <div className="bot-wrapper">
      			<Route exact path="/bot/:id/:page" component={Sidebar} />
            <Switch>
              <Route exact path="/bot/:id/editor" component={Editor} />
              <Route exact path="/bot/:id/analytics" component={Analytics} />
              <Route exact path="/bot/:id/clients" component={Clients} />
              <Route exact path="/bot/:id/mailing" component={Mailing} />
              <Route exact path="/bot/:id/payment" component={Payment} />
              {/*
              <Route exact path="/bot/:id/orders" render={(props) => <Orders {...props} />} />
              <Route exact path="/bot/:id/connect" render={(props) => <Connect {...props} />} />
              <Route exact path="/bot/:id/integration" render={(props) => <Integration {...props} />} />
              <Route exact path="/bot/:id/settings" render={(props) => <Settings {...props} />} />
              <Route exact path="/bot/:id/faq" render={(props) => <FAQ {...props} />} />
              */}
              <Route path='' render={props => <Redirect to="/" />} />
            </Switch>
          </div>
        </div>
      </React.Fragment>
    );
  }

  renderDefaultRoutes(){
    return (
      <div className="app">
        <Switch>
          <Route exact path='/' render={(props) => <Auth {...props} />}/>
          <Route exact path='/reset' render={(props) => <ResetPassword {...props} />}/>
          <Route path="/change_password/:userId/:queryId" render={(props) => <ChangePassword {...props} />} />
          <Route path='' render={props => <Redirect to="/" />} />
        </Switch>
      </div>
    );
  }

  render() {
    return (
      <Router>
        {this.props.isSignedIn ? this.renderAuthenticatedRoutes() : this.renderDefaultRoutes()}
      </Router>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    isSignedIn: state.auth.isSignedIn
  }
};

export default connect(mapStateToProps)(App);
