import React, { Component } from 'react'
import axios from '../api/axios';

// import * as Yup from 'yup';

import CircularProgress from '@material-ui/core/CircularProgress';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableFooter from '@material-ui/core/TableFooter';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

import TablePagination from '../components/TablePagination';
import CreateCampaign from '../components/CreateCampaign';
import NeedSub from '../components/NeedSub';
import PageLoader from '../components/PageLoader';

// const validationSchema =  Yup.object().shape({
//   text: Yup.string()
//   .required(<Translate id="validation.required" />),
// });

export default class Mailing extends Component {
	state = {
		needSub: false,
		data: null,
    pages: null,
    tableLoading: false,
		currentPage: 1
	}

	componentDidMount = () => {
    axios.get(`/api/compaign_list/${this.props.match.params.id}/`)
		.then(res => {
			this.setState({
				data: res.data.results,
				pages: res.data.count
			});
			window.scrollTo({ top: 0, behavior: 'smooth' });
		})
		.catch(err => {
      if(err.response.status === 403){
        this.setState({ needSub: true });
      } else {
        console.log(err);
      }
		});
	}

	handleSubmit = (value) => {
		var data = [...this.state.data];
		data.unshift(value);
		this.setState({ data: data });
	}

  onPageChange = (newPage) => {
    this.setState({ tableLoading: true });
    axios.get(`/api/compaign_list/${this.props.match.params.id}/?page=${newPage}`)
    .then(res => {
			this.setState({
				data: res.data.results,
				pages: res.data.count,
        currentPage: newPage,
        tableLoading: false
			});
    })
    .catch(err => {
      console.log(err);
    })
  }

  renderTableContent(){
    if (!this.state.data.length) {
      return <TableRow><TableCell align="center" colSpan="4"> </TableCell></TableRow>;
    }
  	return this.state.data.map((row, index) => {
  		return (
				<TableRow key={row.id}>
					<TableCell>{row.date_in}</TableCell>
					<TableCell>{row.text}</TableCell>
					<TableCell>
						{row.photo || row.video ? <a href={row.photo || row.video} target="blank"></a> : null}
					</TableCell>
					<TableCell align="right">{row.status}</TableCell>
				</TableRow>
  		);
  	});
  }

	render() {
    if(this.state.needSub){
      return <div className="wrapper"><NeedSub botId={this.props.match.params.id} /></div>
    }
    if(!this.state.data) return <PageLoader />;
		return (
			<div className="wrapper" style={{ display: 'block'}}>
				<CreateCampaign botId={this.props.match.params.id} submit={this.handleSubmit} />
				{this.state.data !== null ? (
					<Paper>
						<Table style={{width: '100%'}}>
							<TableHead style={{background: '#f5f5f5'}}>
								<TableRow>
									<TableCell></TableCell>
									<TableCell></TableCell>
									<TableCell></TableCell>
									<TableCell align="right"></TableCell>
								</TableRow>
							</TableHead>
							<TableBody>
                {this.renderTableContent()}
							</TableBody>
              <TableFooter>
                <TableRow>
                  <TablePagination
                    loading={this.state.tableLoading}
                    currentPage={this.state.currentPage}
                    pages={this.state.pages}
                    onPageChange={this.onPageChange}
                  />
                </TableRow>
              </TableFooter>
						</Table>
					</Paper>
				) : <CircularProgress style={{marginTop: '24px'}} />}
			</div>
		)
	}
}
