import React from 'react';
import axios from 'axios';
import { connect } from 'react-redux';
import * as actions from './../store/actions';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import LoginForm from '../components/auth/LoginForm';
import SignupForm from '../components/auth/SignupForm';

class Auth extends React.Component {
  state = {
    tab: 0,
    authError: false,
    signUpError: false
  }

  onLoginSubmit = values => {
    this.setState({ authError: false })
    axios.post('/api/token/', values)
      .then(({ data }) => {
        this.props.signIn(data);
      })
      .catch(err => {
        this.setState({ authError: err.response.data.detail })
      });
  }

  onSignupSubmit = values => {
    this.setState({ signUpError: false })
    axios.post('/api/register/', values)
      .then(({ data }) => {
        this.props.signIn(data);
      })
      .catch(err => {
        this.setState({ signUpError: err.response.data.detail })
      });
  }

  onTabChange = (event, newValue) => {
    this.setState({ tab: newValue, authError: false, signUpError: false  });
  }

  render(){
    const form = this.state.tab === 0
      ? <LoginForm onSubmit={this.onLoginSubmit} error={this.state.authError} />
      : <SignupForm onSubmit={this.onSignupSubmit} error={this.state.authError} />;
    return (
      <div className="auth-wrapper container">
        <div className="auth">
          <Tabs
            value={this.state.tab}
            indicatorColor="primary"
            textColor="primary"
            onChange={this.onTabChange}
            style={{ marginBottom: 16 }}
          >
            <Tab label="" />
            <Tab label="" />
          </Tabs>

          {form}

        </div>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
	return {
		signIn: (payload) => dispatch({ type: actions.SIGN_IN, payload })
	};
}

export default connect(null, mapDispatchToProps)(Auth);
