import React from 'react'

import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemIcon from '@material-ui/core/ListItemIcon';

import Message from '@material-ui/icons/MessageOutlined';
import DeviceHub from '@material-ui/icons/DeviceHubOutlined';
import MenuIcon from '@material-ui/icons/MenuOutlined';
import InputIcon from '@material-ui/icons/InputOutlined';
import FastForward from '@material-ui/icons/FastForwardOutlined';
import ImageIcon from '@material-ui/icons/ImageOutlined';
import VideoIcon from '@material-ui/icons/MovieOutlined';
import ShoppingCart from '@material-ui/icons/ShoppingCartOutlined';
import PauseIcon from '@material-ui/icons/PauseOutlined';
import AttachFile from '@material-ui/icons/AttachFileOutlined';
import Location from '@material-ui/icons/LocationOnOutlined';
import Contact from '@material-ui/icons/AccountBoxOutlined';

const cardTypes = [
	{
		type: 'text',
		title: ''
	},
	// {
	// 	type: 'fork',
	// 	title: ''
	// },
	{
		type: 'menu',
		title: ''
	},
	// {
	// 	type: 'input',
	// 	title: ''
	// },
	// {
	// 	type: 'rewind',
	// 	title: ''
	// },
	{
		type: 'image',
		title: ''
	},
	{
		type: 'video',
		title: ''
	},
	// {
	// 	type: 'file',
	// 	title: ''
	// },
	// {
	// 	type: 'order',
	// 	title: ''
	// },
	// {
	// 	type: 'pause',
	// 	title: ''
	// },
	// {
	// 	type: 'contact',
	// 	title: ''
	// },
	// {
	// 	type: 'geo',
	// 	title: ''
	// }
];

const listIcon = type => {
	switch (type) {
		case 'text':
			return <Message />;
		case 'fork':
			return <DeviceHub />
		case 'menu':
			return <MenuIcon />
		case 'input':
			return <InputIcon />
		case 'rewind':
			return <FastForward />
		case 'image':
			return <ImageIcon />
		case 'video':
			return <VideoIcon />
		case 'file':
			return <AttachFile />
		case 'order':
			return <ShoppingCart />
		case 'pause':
			return <PauseIcon />
		case 'contact':
			return <Contact />
		case 'geo':
			return <Location />
		default:
			return null;
	}
}

export default props => {
	const handleListClick = type => {
		props.onClose(type);
	}

	const onClose = () => {
		props.onClose(null);
	}
	return (
		<div>
			<Dialog onClose={onClose} open={props.open}>
				<DialogTitle> </DialogTitle>
				<List style={{minWidth: '344px'}}>
					{cardTypes.map(el => (
						<ListItem button onClick={() => handleListClick(el.type)} key={el.type}>
							<ListItemIcon>
								{listIcon(el.type)}
							</ListItemIcon>
							<ListItemText inset primary={el.title} />
						</ListItem>
					))}
				</List>
			</Dialog>
		</div>
	)
}
