import React, { Component } from 'react'
import axios from '../api/axios';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableFooter from '@material-ui/core/TableFooter';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

import NeedSub from '../components/NeedSub';
import TablePagination from '../components/TablePagination';
import PageLoader from '../components/PageLoader';

export default class Users extends Component {
	state = {
		data: null,
    pages: null,
    tableLoading: false,
		currentPage: 1,
    needSub: false
	}

	componentDidMount = () => {
		axios.get(`/api/botusers/${this.props.match.params.id}/`)
		.then(res => {
			this.setState({
				data: res.data.results,
				pages: res.data.count
			});
			window.scrollTo({ top: 0, behavior: 'smooth' });
		})
		.catch(err => {
      if(err.response.status === 403){
        this.setState({ needSub: true });
      } else {
        console.log(err);
      }
		});
	}

  onPageChange = (newPage) => {
    this.setState({ tableLoading: true });
    axios.get(`/api/botusers/${this.props.match.params.id}/?page=${newPage}`)
    .then(res => {
			this.setState({
				data: res.data.results,
				pages: res.data.count,
        currentPage: newPage,
        tableLoading: false
			});
    })
    .catch(err => {
      console.log(err);
    })
  }

  renderTableContent(){
    if (!this.state.data.length) {
      return <TableRow><TableCell align="center" colSpan="4"> </TableCell></TableRow>;
    }
  	return this.state.data.map((client, index) => {
  		return (
  			<TableRow key={client.id}>
  				<TableCell>{client.first_name} {client.last_name}</TableCell>
  				<TableCell>{client.username}</TableCell>
  				<TableCell>{client.chat_id}</TableCell>
  				<TableCell align="right">{client.date_in}</TableCell>
  			</TableRow>
  		);
  	});
  }

	render() {
    if(this.state.needSub){
      return <div className="wrapper"><NeedSub botId={this.props.match.params.id}/></div>
    }
		if(!this.state.data) return <PageLoader />;
		return (
			<div className="wrapper">
				<Paper>
					<Table style={{width: '100%'}}>
						<TableHead style={{background: '#f5f5f5'}}>
							<TableRow>
								<TableCell></TableCell>
								<TableCell></TableCell>
								<TableCell>ID </TableCell>
								<TableCell align="right"></TableCell>
							</TableRow>
						</TableHead>
						<TableBody>
              {this.renderTableContent()}
						</TableBody>
            <TableFooter>
              <TableRow>
                <TablePagination
                  loading={this.state.tableLoading}
                  currentPage={this.state.currentPage}
                  pages={this.state.pages}
                  onPageChange={this.onPageChange}
                />
              </TableRow>
            </TableFooter>
					</Table>
				</Paper>
			</div>
		)
	}
}
