import React, { Component } from 'react';
import { connect } from 'react-redux';
import getPages from '../../helpers/getPages';

import Button from '@material-ui/core/Button';
import DeleteIcon from '@material-ui/icons/Delete';
import IconButton from '@material-ui/core/IconButton';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import {Formik, Form, FieldArray, Field} from 'formik';

class ForkEditor extends Component {
	handleClose = () => {
		this.props.editCard(null);
	}

	handleSubmit = () => {
		this.form.submitForm();
	}

	onSubmit = (values, actions) => {
		console.log(values);
		var data = {
			type: "fork",
			value: values.forks
		}
		this.props.editCard(data);
	}

	render() {
		var pages = getPages(this.props.editor, 'option');
		console.log(pages);
		var initialValues = {
			forks: [
				{ 
					text: '',
					id: pages[0].value 
				}
			]
		}

		if (this.props.value !== null) {
			initialValues = {
				forks: this.props.value
			}
		}
		return (
			<Dialog open={true}>
				<DialogTitle></DialogTitle>
				<DialogContent>
					<Formik 
						onSubmit={this.onSubmit}
						initialValues={initialValues}
						ref={node => (this.form = node)}>
							{({ handleSubmit, handleChange, handleBlur, values, errors, touched }) => (
								<Form onSubmit={handleSubmit} style={{minWidth: '355px'}}>
								  <FieldArray
										name="forks"
										render={arrayHelpers => (
											<div>
												{values.forks.map((fork, index) => (
													<div key={index} className="editor-field-group">
														<Field name={`forks[${index}]text`}>{({ field, form }) => (
															<TextField
																{...field}
																variant="outlined"
																label=""
																style={{ marginRight: '16px', marginTop: '8px'}}
															/>
														)}</Field>
														<Field name={`forks[${index}]id`}>{({ field, form }) => (
															<TextField 
																{...field}
																select
																variant="outlined"
																label=""
																SelectProps={{
																	native: true
																}}
																InputLabelProps={{
																	shrink: true,
																}}
																style={{ marginTop: '8px'}}
															>
																{pages.map(option => (
																	<option key={option.value} value={option.value}>
																		{option.label}
																	</option>
																))}
															</TextField>
														)}
														</Field>
														<IconButton onClick={() => arrayHelpers.remove(index)} aria-label="Delete">
															<DeleteIcon />
														</IconButton>
													</div>
												))}
												<div className="text-center" style={{ marginTop: '16px'}}>
													<Button 
														onClick={() => arrayHelpers.push({ text: '', id: pages[0].value })} 
														color="primary"
														variant="outlined"
													>
														<i className="material-icons" style={{ marginRight: '4px'}}>add</i>
														
													</Button>
												</div>
											</div>
										)}
									/>
								</Form>
							)}
						</Formik>
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color="primary">
              
            </Button>
            <Button onClick={this.handleSubmit} color="primary">
              Co
            </Button>
          </DialogActions>
			</Dialog>
		)
	}
}

const mapStateToProps = state => {
	return {
		editor: state.editor
	}
}

export default connect(mapStateToProps)(ForkEditor);