import React, { Component } from 'react'
import * as Yup from 'yup';
import { connect } from 'react-redux';
import getPages from '../../helpers/getPages';

import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import {Formik, Form, Field} from 'formik'
import { Translate } from "react-localize-redux";

const validationSchema =  Yup.object().shape({
  page: Yup.string()
  .required(<Translate id="validate.required" />),
});

class RewindEditor extends Component {
	handleClose = () => {
		this.props.editCard(null);
	}

	handleSubmit = () => {
		this.form.submitForm()
	}

	onSubmit = (values, actions) => {
		var data = {
			type: "rewind",
			value: values.page
		}
		this.props.editCard(data);
	}

	render() {
		var pages = getPages(this.props.editor, 'options');
		var initialValues = {
			page: pages[0].id
		};

		if (this.props.value !== null) {
			initialValues = {
				page: this.props.value
			}
		}
		return (
			<Dialog open={true}>
				<DialogTitle><Translate id="editor.elements.rewind" /></DialogTitle>
				<DialogContent>
					<Formik
						onSubmit={this.onSubmit}
						initialValues={initialValues}
						validationSchema={validationSchema}
						ref={node => (this.form = node)}>
							{({ handleSubmit, handleChange, handleBlur, values, errors, touched }) => (
								<Form onSubmit={handleSubmit} style={{minWidth: '344px'}}>
									<Field name="page">{({ field, form }) => (
										<TextField
											{...field}
											select
											variant="outlined"
											label={<Translate id="forms.screen" />}
											error={touched.page && Boolean(errors.page)}
											helperText={errors.page && touched.page ? errors.page : "" }
											SelectProps={{
												native: true
											}}
											InputLabelProps={{
												shrink: true,
											}}
											style={{ marginTop: '8px'}}
										>
											{pages.map(option => (
												<option key={option.id} value={option.id}>
													{option.name}
												</option>
											))}
										</TextField>
									)}</Field>
									<br/>
								</Form>
							)}
						</Formik>
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color="primary">
              <Translate id="btn.cancel" />
            </Button>
            <Button onClick={this.handleSubmit} color="primary">
              <Translate id="btn.save" />
            </Button>
          </DialogActions>
			</Dialog>
		)
	}
}

const mapStateToProps = state => {
	return {
		editor: state.editor
	}
}

export default connect(mapStateToProps)(RewindEditor);
