import React from 'react';
import { connect } from "react-redux";
import axios from '../api/axios';

import NeedSub from '../components/NeedSub';
import ClientsCount from '../components/ClientsCount';

import CircularProgress from '@material-ui/core/CircularProgress';

class Analytics extends React.Component {
  state = {
    needSub: false,
    data: null
  }

  componentDidMount = () => {
    axios.get(`/api/analytics_data/${this.props.match.params.id}/`)
    .then(res => {
      this.setState({ data: res.data });
    })
    .catch(err => {
      if(err.response.status === 403){
        this.setState({ needSub: true });
      }
    });
  }

  render () {
    if(this.state.needSub){
      return <div className="wrapper"><NeedSub botId={this.props.match.params.id}/></div>
    }
    if(!this.state.data){
      return (
        <div className="wrapper wrapper-oh">
          <div className="wrapper_loading" >
  	         <CircularProgress />
  			  </div>
        </div>
      )
    }
    return (
      <div className="wrapper">
        <div className="row">
          <div className="col-xl-4">
            <ClientsCount color="#61b5ff" title=" " count={this.state.data.all_users_count} />
          </div>
          <div className="col-xl-4">
            <ClientsCount color="#80CBC4" title=" " count={this.state.data.today_users_count} />
          </div>
          <div className="col-xl-4">
            <ClientsCount color="#EF9A9A" title="" count={this.state.data.unsubscribe_users_count} />
          </div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
  }
}

const mapDispatchToProps = (dispatch) => {
	return {
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(Analytics);
