import React from 'react';
import { connect } from 'react-redux';
import * as actions from '../store/actions';
import * as Yup from 'yup';
import axios from 'axios';
import { Redirect, Link } from 'react-router-dom';

import { Formik, Form } from 'formik'
import TextField from '@material-ui/core/TextField';
import CircularProgress from '@material-ui/core/CircularProgress';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import DoneOutlineIcon from '@material-ui/icons/DoneOutlineRounded';
import Zoom from '@material-ui/core/Zoom';

const validationSchema =  Yup.object().shape({
	email: Yup.string()
	.required("  !")
});


class ResetPassword extends React.Component {
	state = {
		loading: false,
    success: false,
		error: null
	}

	onSubmit = (values) => {
		this.setState({ loading: true })
		axios.post('/api/forgot_password/', {
			email: values.email
		})
		.then(({ data }) => {
    	this.setState({
				loading: false,
				success: true,
				error: null
			});
		})
		.catch(err => {
			this.setState({
				loading: false,
				error: err.response.data.error
			});
		});
	}

	render() {
		if (null) {
			return <Redirect to='/exchange' />;
		}
		return (
			<main className="auth-wrapper container">
				<header className="auth-header">
					<Link to="/" className="auth_logo">
  					<Typography align="center" color="primary" variant="h5" gutterBottom>INBOT24</Typography>
					</Link>
				</header>

				{!this.state.loading &&!this.state.success ? <div className="auth-form">
					<Typography align="center" variant="h5" gutterBottom>
             
					</Typography>
					<Formik
						onSubmit={this.onSubmit}
						initialValues={{
							email: ''
						}}
						validationSchema={validationSchema}
						ref={node => (this.form = node)}>
							{({ handleSubmit, handleChange, handleBlur, values, errors, touched }) => (
								<Form onSubmit={handleSubmit}>
									<TextField
										type="email"
										name="email"
										label="Email"
										margin="normal"
										variant="outlined"
										value={values.email}
										onChange={handleChange}
										onBlur={handleBlur}
										error={touched.email && Boolean(errors.email)}
										helperText={errors.email && touched.email ? errors.email : "" }
										required
										style={{minWidth: "300px"}}
									/>
									{!this.state.loading && this.state.error ? <div>
					  					<Typography className="auth_error" variant="body2" color="error" gutterBottom style={{ marginLeft: 12 }}>
												{this.state.error}
					  					</Typography>
									</div> : null}
									<div className="text-center">
										<Button
											type="submit"
											variant="contained"
											color="primary"
											size="large"
											style={{ marginTop: 14 }}
										>
											
										</Button>
									</div>
								</Form>
							)}
						</Formik>
				</div> : null}
        {this.state.success ? (
          <div>
            <Zoom in={true} timeout={300}>
              <div className="text-center">
                <DoneOutlineIcon color="primary" style={{ fontSize: 64 }} />
              </div>
            </Zoom>
  					<Typography align="center" variant="h5" gutterBottom> !</Typography>
  					<Typography align="center" variant="body1" gutterBottom>
                       
  					</Typography>
          </div>
        ) : null}
        {this.state.loading ? <CircularProgress /> : null}
			</main>
		);
	}
}


const mapDispatchToProps = (dispatch) => {
	return {
		auth: (userData) => dispatch({ type: actions.SIGN_IN, userData }),
	}
}

export default connect(null, mapDispatchToProps)(ResetPassword);
