import React, { Component } from 'react'

import TextEditor from '../cardEditor/TextEditor';
import ForkEditor from '../cardEditor/ForkEditor';
import MenuEditor from '../cardEditor/MenuEditor';
import InputEditor from '../cardEditor/InputEditor';
import RewindEditor from '../cardEditor/RewindEditor';
import ImageEditor from '../cardEditor/ImageEditor';
import FileEditor from '../cardEditor/FileEditor';
import OrderEditor from '../cardEditor/OrderEditor';
import PauseEditor from '../cardEditor/PauseEditor';
import ContactEditor from '../cardEditor/ContactEditor';
import GeoEditor from '../cardEditor/GeoEditor';
import VideoEditor from '../cardEditor/VideoEditor';

// Card types

export default class cardEditor extends Component {
	getEditor = () => {
		switch (this.props.type) {
			case 'text':
				return <TextEditor editCard={this.props.onCreate} data={null} />;
			case 'fork':
				return <ForkEditor editCard={this.props.onCreate} data={null} />;
			case 'menu':
				return <MenuEditor editCard={this.props.onCreate} data={null} />;
			case 'input':
				return <InputEditor editCard={this.props.onCreate} data={null} />;
			case 'rewind':
				return <RewindEditor editCard={this.props.onCreate} data={null} />;
			case 'image':
				return <ImageEditor editCard={this.props.onCreate} data={null} botId={this.props.botId} />;
			case 'video':
				return <VideoEditor editCard={this.props.onCreate} data={null} botId={this.props.botId} />;
			case 'file':
				return <FileEditor editCard={this.props.onCreate} data={null} botId={this.props.botId} />;
			case 'order':
				return <OrderEditor editCard={this.props.onCreate} data={null} />;
			case 'pause':
				return <PauseEditor editCard={this.props.onCreate} data={null} />;
			case 'contact':
				return <ContactEditor editCard={this.props.onCreate} data={null} />;
			case 'geo':
				return <GeoEditor editCard={this.props.onCreate} data={null} />;
			default:
				return null;
		}
	}

	render() {
		return (
			this.props.open ? this.getEditor() : null
		)
	}
}
