import React from 'react';
import { NavLink } from 'react-router-dom';
import EditorIcon from '@material-ui/icons/ExtensionOutlined';
import MailingIcon from '@material-ui/icons/SendOutlined';
import AnalyticsIcon from '@material-ui/icons/TrendingUpOutlined';
// import OrdersIcon from '@material-ui/icons/AssignmentOutlined';
import ClinetsIcon from '@material-ui/icons/FaceOutlined';
import PaymentIcon from '@material-ui/icons/MonetizationOnOutlined';

const Sidebar = ({ match }) => {
    return (
        <aside className="aside">
					<ul>
						<li>
              <NavLink to={`/bot/${match.params.id}/editor`} activeClassName="active">
                <EditorIcon className="aside_icon"/>
                -
              </NavLink>
            </li>
						<li>
              <NavLink to={`/bot/${match.params.id}/analytics`} activeClassName="active">
                <AnalyticsIcon className="aside_icon"/>
                
              </NavLink>
            </li>
						<li>
              <NavLink to={`/bot/${match.params.id}/clients`} activeClassName="active">
                <ClinetsIcon className="aside_icon"/>
                
              </NavLink>
            </li>
						<li>
              <NavLink to={`/bot/${match.params.id}/mailing`} activeClassName="active">
                <MailingIcon className="aside_icon"/>
                
              </NavLink>
            </li>
						<li>
              <NavLink to={`/bot/${match.params.id}/payment`} activeClassName="active">
                <PaymentIcon className="aside_icon"/>
                
              </NavLink>
            </li>
            {/*
						<li>
              <NavLink to={`/bot/${match.params.id}/orders`} activeClassName="active">
                <OrdersIcon className="aside_icon"/>
                
              </NavLink>
            </li>
						<li><NavLink to={`/bot/${match.params.id}/connect`} activeClassName="active"></NavLink></li>
						<li><NavLink to={`/bot/${match.params.id}/payment`} activeClassName="active"><i className="material-icons">monetization_on</i><Translate id="sidebar.payment" /></NavLink></li>
						<li><NavLink to={`/bot/${match.params.id}/integration`} activeClassName="active"></NavLink></li>
						<li><NavLink to={`/bot/${match.params.id}/faq`} activeClassName="active"></NavLink></li>
						<li><NavLink to={`/bot/${match.params.id}/settings`} activeClassName="active"></NavLink></li>
            */}
					</ul>
        </aside>
		);
}

export default Sidebar;
