import React, { Component } from 'react';

import * as actions from '../store/actions';
import { connect } from 'react-redux';
import { Draggable, Droppable } from 'react-beautiful-dnd';

import Page from './Page';
import CreatePage from './CreatePage';
import OutsideClickHandler from 'react-outside-click-handler';

class Chain extends Component {
	state = {
		adder: false,
	}

	handlePageAddTrigger = () => {
		this.setState({adder: true});
	}

	handleOutsideClick = () => {
		this.setState({adder: false});
	}

	handlePageAddSubmit = (values, action) => {
		this.props.addPage(this.props.chainIndex, values.pageName)
		this.setState({adder: false});
	}

	handleCardCreate = (pageId, data) => {
		this.props.addElement(this.props.index, pageId, data);
	}

	handleCardDelete = (pageId, id) => {
		this.props.removeElement(this.props.index, pageId, id);
	}

	handleCardEdit = (pIndex, eIndex, value) => {
		this.props.editElement(this.props.index, pIndex, eIndex, value);
	}

	render() {
		var adder = <div className="add-page_btn"><i className="material-icons">add</i>  </div>;
		if (this.state.adder) {
			adder = <CreatePage onSubmit={this.handlePageAddSubmit} />;
		}
		return (
			<div className="chain">
					<Droppable
						type="page"
						droppableId={this.props.chainIndex+""}>
	          {provided => (
							<div
								{...provided.droppableProps}
								ref={provided.innerRef}
								className="page-droppable"
							>
								{
									this.props.data.map((page, index) =>
										<Draggable
							        draggableId={"page:"+this.props.chainIndex+":"+index+":"+page.id}
							        index={index}
											key={page.id}
											isDragDisabled={true ? true : this.props.editorDragDisabled}
										>
											{provided => (
												<div
													ref={provided.innerRef}
													{...provided.draggableProps}
													{...provided.dragHandleProps}
													className="page-container"
												>
													<Page
														botId={this.props.botId}
														handleCardCreate={this.handleCardCreate}
														handleCardDelete={this.handleCardDelete}
														handleCardEdit={this.handleCardEdit}
														key={page.id}
														pageId={page.id}
														chainIndex={this.props.chainIndex}
														pageIndex={index}
														{...page}
													/>
												</div>
											)}
										</Draggable>
								)}
								{provided.placeholder}
							</div>
	          )}
					</Droppable>
					<OutsideClickHandler onOutsideClick={this.handleOutsideClick}>
						<div
							className={this.state.adder ? "add-page add-page--active" : "add-page"} onClick={this.handlePageAddTrigger}
						>
							{adder}
						</div>
					</OutsideClickHandler>

			</div>
		)
	}
}

const mapStateToProps = (state) => {
	return {
		editorDragDisabled: state.editorDragDisabled
	}
}

const mapDispatchToProps = (dispatch) => {
	return {
		addPage: (chainIndex, pageName) => dispatch({ type: actions.ADD_PAGE, chainIndex, pageName })
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(Chain);
