import React, { Component } from 'react';
import * as actions from '.././store/actions';
import { connect } from 'react-redux';
import { Draggable, Droppable } from 'react-beautiful-dnd';

import OutsideClickHandler from 'react-outside-click-handler';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/DeleteForeverOutlined';
import AddIcon from '@material-ui/icons/AddOutlined';
// import MoreIcon from '@material-ui/icons/MoreVert';
// import DuplicateIcon from '@material-ui/icons/FileCopy';
// import Menu from '@material-ui/core/Menu';
// import MenuItem from '@material-ui/core/MenuItem';
// import ListItemIcon from '@material-ui/core/ListItemIcon';
// import ListItemText from '@material-ui/core/ListItemText';

import CardTypeSelector from './card/CardTypeSelector';
import CardCreator from './card/CardCreator';
import Element from './card/Element';

class Page extends Component {
	state = {
		typeSelector: false,
		cardCreator: false,
		newCardType: null,
		titleEditing: false,
		menu: null,
		newTtile: ''
	}

	handleCardCreateTrigger = () => {
		this.setState({ typeSelector: true });
	}

	handleTypeSelectorClose = type => {
		this.setState({ typeSelector: false });
		if (type === null) { return };

		this.setState({ cardCreator: true, newCardType: type });
	}

	handleCardCreate = (data) => {
		if (data === null) {
			this.setState({ cardCreator: false, newCardType: null});
			return;
		}
		this.props.createCard(this.props.chainIndex, this.props.pageIndex, data);
		this.setState({ cardCreator: false, newCardType: null});
	}

	handleTitleEditingToggle = () => {
		this.setState({
			titleEditing: true,
			newTitle: this.props.name
		});
	}

	handlePageTitleChange = (event) => {
		this.setState({ newTitle: event.target.value });
	}

	handleTitleEditingFinish = () => {
		if(this.state.newTitle.length > 0){
			this.setState({ titleEditing: false });
			if (this.props.name !== this.state.newTitle) {
				this.props.editPageTitle(this.props.chainIndex, this.props.pageIndex, this.state.newTitle);
			}
		} else {
			this.setState({ titleEditing: false, newTitle: '' });
		}
	}

	toggleMenu = (state) => {
		this.setState({ menu: state });
	}

	handleDuplicate = () => {
		this.setState({ menu: null });
		this.props.duplicatePage(this.props.chainIndex, this.props.pageIndex)
	}

	handleDelete = () => {
		this.setState({ menu: null });
		this.props.removePage(this.props.chainIndex, this.props.pageIndex)
	}

  render() {
    return (
			<div className="page">
				<header className="page-header">
					{
						this.state.titleEditing
						?	<OutsideClickHandler onOutsideClick={this.handleTitleEditingFinish}>
								<TextField
					        multiline
									value={this.state.newTitle}
									label=" "
					        margin="normal"
					        variant="outlined"
									required
									onChange={this.handlePageTitleChange}
									fullWidth={true}
									autoFocus={true}
					      />
							</OutsideClickHandler>
						: <div className="page-title" >{this.props.name}</div>
					}

					{/*
					<div className="page-menu">
						<IconButton aria-label="Screen menu" onClick={(e) => this.toggleMenu(e.currentTarget)}>
	            <MoreIcon />
	          </IconButton>
			      <Menu
			        keepMounted
							anchorEl={this.state.menu}
			        open={Boolean(this.state.menu)}
			        onClose={() => this.toggleMenu(null)}
			      >
			        <MenuItem onClick={this.handleDuplicate}>
								<ListItemIcon><DuplicateIcon /></ListItemIcon>
								<ListItemText primary="" />
							</MenuItem>
			        <MenuItem onClick={this.handleDelete}>
								<ListItemIcon><DeleteIcon /></ListItemIcon>
								<ListItemText primary="" />
							</MenuItem>
			      </Menu>
			    </div>
					*/}
					<IconButton aria-label="Delete" className="page-remove" onClick={() => this.props.removePage(this.props.chainIndex, this.props.pageIndex)}>
            <DeleteIcon />
          </IconButton>
				</header>
				<Droppable
					type="card"
					droppableId={this.props.pageId+':'+this.props.chainIndex+':'+this.props.pageIndex}>
          {provided => (
						<div
							className="page-cards"
							{...provided.droppableProps}
							ref={provided.innerRef}
							style={{marginBottom: 14}}
						>

							{this.props.elements.map((el, index) => (
									<Draggable
						        draggableId={"card:"+el.id+":"+this.props.chainIndex+":"+this.props.pageIndex}
										chainIndex={this.props.chainIndex}
										pageIndex={this.props.pageIndex}
						        index={index}
										key={el.id}
										isDragDisabled={true ? true : this.props.editorDragDisabled}
									>
										{provided => (
											<div
												ref={provided.innerRef}
												{...provided.draggableProps}
												{...provided.dragHandleProps}
												className="card-container"
											>
												<Element
													id={el.id}
													botId={this.props.botId}
													chainIndex={this.props.chainIndex}
													pageIndex={this.props.pageIndex}
													index={index}
													type={el.type}
													data={el.data}
													key={el.id}
												/>
											</div>
										)}
									</Draggable>
								))
							}
							{provided.placeholder}
						</div>
          )}
				</Droppable>
				<footer className="add-card">
					<Button
						color="secondary"
						fullWidth
						onClick={this.handleCardCreateTrigger}
					>
						<AddIcon style={{ marginRight: 8 }} />
						 
					</Button>
					<CardTypeSelector
						open={this.state.typeSelector}
						onClose={this.handleTypeSelectorClose}
					/>
					<CardCreator
						botId={this.props.botId}
						open={this.state.cardCreator}
						type={this.state.newCardType}
						onCreate={this.handleCardCreate}
					/>
				</footer>
				{/*
				<div className="page-divider"></div>
				*/}
			</div>
    )
  }
}

const mapStateToProps = (state) => {
	return {
		editorDragDisabled: state.editorDragDisabled
	}
}

const mapDispatchToProps = (dispatch) => {
	return {
		createCard: (chainIndex, pageIndex, data) => dispatch({ type: actions.ADD_ELEMENT, chainIndex, pageIndex, data }),
		removePage: (chainIndex, pageIndex) => dispatch({ type: actions.REMOVE_PAGE, chainIndex, pageIndex }),
		editPageTitle: (chainIndex, pageIndex, newTitle) => dispatch({ type: actions.EDIT_PAGE_TITLE, chainIndex, pageIndex, newTitle }),
		duplicatePage: (chainIndex, pageIndex) => dispatch({ type: actions.DUPLICATE_PAGE, chainIndex, pageIndex })
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(Page);
