import React, { Component } from 'react';

import Typography from '@material-ui/core/Typography';
import CardContent from '@material-ui/core/CardContent';
import GeoIcon from '@material-ui/icons/LocationOn';
import { Translate } from "react-localize-redux";

class Element extends Component {
	render() {
		return (
			<CardContent style={{paddingBottom: 0}}>
				<div className="card-type">
					<GeoIcon className="card-type_icon" style={{ fontSize: 16 }}/><Translate id="editor.elements.geo" />
				</div>
				<Typography component="div" className="card-p">
					<Typography className="card-p"><Translate id="forms.latitude"/>: {this.props.value.latitude}</Typography>
					<Typography className="card-p"><Translate id="forms.longitude"/>: {this.props.value.longitude}</Typography>
				</Typography>
			</CardContent>
		)
	}
}

export default Element;
