import React, { Component } from 'react'
import * as Yup from 'yup';
import axios from 'axios';

import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import CircularProgress from '@material-ui/core/CircularProgress';
import {Formik, Form} from 'formik';
import { Translate } from "react-localize-redux";


const validationSchema =  Yup.object().shape({
	file: Yup.mixed()
	.required(<Translate id="validation.required" />),
});

export default class ImageEditor extends Component {
	state = {
		tooLarge: false,
		serverError: false,
		submiting: false
	}
	
	handleClose = () => {
		this.props.editCard(null);
	}

	handleSubmit = () => {
		this.form.submitForm()
	}

	onSubmit = (values, actions) => {
		this.setState({ submiting: true });
		var data = new FormData();
		data.append('file', values.file);
		axios.post(`upload_file/`, data, {
			crossDomain: true,
			withCredentials: true
		})
		.then(res => {
			if (res.data.error) {
				this.setState({ serverError: res.data.error })
				return
			}
			var data = {
				type: "file",
				value: res.data.url,
			}
			this.setState({ submiting: false });
			this.props.editCard(data);
			actions.setSubmitting(false);
		})
		.catch(err => {
			console.log(err);
			actions.setSubmitting(false);
		})

	}

	render() {
		if (this.state.submiting === true){
			var submiting = <div className="submiting" >
				<CircularProgress />
			</div>
		}

		return (
			<Dialog open={true}>
				<DialogTitle><Translate id="editor.elements.file" /></DialogTitle>
				<DialogContent>
					{submiting}
					<Formik 
						onSubmit={this.onSubmit}
						validationSchema={validationSchema}
						ref={node => (this.form = node)}>
							{({ handleSubmit, handleChange, handleBlur, values, errors, touched, setFieldValue }) => (
								<Form onSubmit={handleSubmit} style={{minWidth: '344px'}}>
									<input name="file" type="file" onChange={(event) => {
											this.setState({ serverError: false, tooLarge: false });
											if (((event.currentTarget.files[0].size/1024)/1024).toFixed(4) <= 1) {
												setFieldValue("file", event.currentTarget.files[0]);	
											} else {
												setFieldValue("file", "");
												this.setState({ tooLarge: true });
											}
											
									}} />
									{this.props.value === null ? <br/> : null}
									{this.state.serverError ? <div className="field-error">{this.state.serverError}</div> : null}
									{this.state.tooLarge ? <div className="field-error">The file size is too large. Max file size: 1MB</div> : null}
								</Form>
							)}
						</Formik>
					</DialogContent>
					<DialogActions>
						<Button onClick={this.handleClose} color="primary">
							<Translate id="btn.cancel" />
						</Button>
						<Button onClick={this.handleSubmit} color="primary">
							<Translate id="btn.save" />
						</Button>
					</DialogActions>
			</Dialog>
		)
	}
}
