import React, { Component } from 'react'
import * as Yup from 'yup';

import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import {Formik, Form} from 'formik'
import { Translate } from "react-localize-redux";

const validationSchema =  Yup.object().shape({
  contact: Yup.string()
  .required(<Translate id="validation.required" />),
});

export default class ContactEditor extends Component {
	handleClose = () => {
		this.props.editCard(null);
	}

	handleSubmit = () => {
		this.form.submitForm()
	}

	onSubmit = (values, actions) => {
		var data = {
			type: "contact",
			value: values.contact
		}
		this.props.editCard(data);
	}

	render() {
		var initialValues = {
			contact: ''
		};

		if (this.props.value !== null) {
			initialValues = {
				contact: this.props.value
			}
		}
		return (
			<Dialog open={true}>
				<DialogTitle><Translate id="editor.elements.contact" /></DialogTitle>
				<DialogContent>
					<Formik
						onSubmit={this.onSubmit}
						initialValues={initialValues}
						validationSchema={validationSchema}
						ref={node => (this.form = node)}>
							{({ handleSubmit, handleChange, handleBlur, values, errors, touched }) => (
								<Form onSubmit={handleSubmit} style={{minWidth: '344px'}}>
									<TextField
										name="contact"
										value={values.contact}
										label={<Translate id="forms.phone" />}
										margin="normal"
										variant="outlined"
										onChange={handleChange}
										onBlur={handleBlur}
										error={touched.contact && Boolean(errors.contact)}
										helperText={errors.contact && touched.contact ? errors.contact : "" }
										required
										style={{width: "100%"}}
									/>
									<br/>
								</Form>
							)}
						</Formik>
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color="primary">
              <Translate id="btn.cancel" />
            </Button>
            <Button onClick={this.handleSubmit} color="primary">
              <Translate id="btn.save" />
            </Button>
          </DialogActions>
			</Dialog>
		)
	}
}
