import React, { Component } from 'react'
import { DragDropContext } from 'react-beautiful-dnd';

import axios from '../api/axios';
import { connect } from 'react-redux';
import * as actions from '../store/actions';

import Button from '@material-ui/core/Button';
import CircularProgress from '@material-ui/core/CircularProgress';
import AddIcon from '@material-ui/icons/AddOutlined';
import Chain from '../components/Chain';

class Editor extends Component {
	componentDidMount = () => {
		if(!this.props.editor){
			axios.get(`/api/bot_data/${this.props.match.params.id}/`, {
				crossDomain: true,
				withCredentials: true
			})
			.then(res => {
				this.props.loadEditorData(res.data);
			})
			.catch(err => {
				console.log(err);
			});
		}
	}

  onDragEnd = result => {
    const { destination, source, draggableId } = result;
		this.props.moveElement(destination, source, draggableId);
  }


  render() {
		var chains;
		if (this.props.editor === null) {
			chains = <div className="text-center flex-grow"><CircularProgress /></div>
		} else {
			chains = this.props.editor.data.map((el, index) => <Chain
				botId={this.props.match.params.id}
				key={index}
				chainIndex={index}
				data={el}
				addElement={this.addElement}
				removeElement={this.removeElement}
				editElement={this.editElement}
				 />)
		}
    return (
      <div className="bot">
				<DragDropContext
					onDragEnd={this.onDragEnd}
				>
					{chains}
				</DragDropContext>

				{
					this.props.editor !== null ? (
						<Button
							size="large"
							variant="outlined"
							color="primary"
							onClick={this.props.addChain}
							className="add-chain"
						>
							<AddIcon
								style={{ marginRight: 4 }}
							/>
							 
						</Button>
					) : null
				}

      </div>
    )
  }
}

const mapStateToProps = (state) => {
	return {
		editor: state.editor,
		editorChanges: state.editorChanges
	}
}

const mapDispatchToProps = (dispatch) => {
	return {
		loadEditorData: (editor) => dispatch({ type: actions.LOAD_EDITOR_DATA, editor: editor }),
		clearEditorData: () => dispatch({ type: actions.CLEAR_EDITOR_DATA }),
		addChain: () => dispatch({ type: actions.ADD_CHAIN }),
		moveElement: (destination, source, draggableId) => dispatch({ type: actions.MOVE_ELEMENT, destination, source, draggableId })
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(Editor);
