export default (editor, format) => {
	if (!editor) return;
	var res = [];
	if (format === 'option') {
		editor.data.forEach(chain => chain.forEach(page => {
			res.push({ value: page.id, label: page.name});
		}));
	} else {
		editor.data.forEach(chain => chain.forEach(page => {
			res.push({ id: page.id, name: page.name});
		}));
	}
	return res;
}
