import React, { Component } from 'react';
import { connect } from 'react-redux';
import getPages from '../../helpers/getPages';

import Button from '@material-ui/core/Button';
import DeleteIcon from '@material-ui/icons/DeleteOutlined';
import IconButton from '@material-ui/core/IconButton';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import {Formik, Form, FieldArray, Field} from 'formik';

class MenuEditor extends Component {
	handleClose = () => {
		this.props.editCard(null);
	}

	handleSubmit = () => {
		this.form.submitForm();
	}

	onSubmit = (values, actions) => {
		var data = {
			type: "menu",
			data: {
				text: values.text,
				fallback: values.fallback,
				value: values.forks
			}
		}
		this.props.editCard(data);
	}

	render() {
		var pages = getPages(this.props.editor, 'option');
		var initialValues = {
			text: '',
			fallback: '',
			forks: [
				{
					text: '',
					id: pages[0].value
				}
			]
		}

		if (this.props.data !== null) {
			initialValues = {
				text: this.props.data.text,
				fallback: this.props.data.fallback,
				forks: this.props.data.value
			}
		}
		return (
			<Dialog open={true}>
				<DialogTitle></DialogTitle>
				<DialogContent>
					<Formik
						onSubmit={this.onSubmit}
						initialValues={initialValues}
						ref={node => (this.form = node)}>
							{({ handleSubmit, handleChange, handleBlur, values, errors, touched }) => (
								<Form onSubmit={handleSubmit} style={{minWidth: '355px'}}>
									<TextField
										name="text"
										value={values.descr}
										multiline
										rowsMax={16}
										rows={6}
										label=""
										margin="normal"
										variant="filled"
										onChange={handleChange}
										onBlur={handleBlur}
										style={{width: "100%", marginBottom: '16px'}}
									/>
								  <FieldArray
										name="forks"
										render={arrayHelpers => (
											<div>
												{values.forks.map((fork, index) => (
													<div key={index} className="editor-field-group">
														<Field name={`forks[${index}]text`}>{({ field, form }) => (
															<TextField
																{...field}
																variant="filled"
																label=" "
																style={{ marginRight: '16px', marginTop: '8px', flex: 1 }}
															/>
														)}</Field>
														<Field name={`forks[${index}]id`}>{({ field, form }) => (
															<TextField
																{...field}
																select
																variant="filled"
																label=""
																SelectProps={{
																	native: true
																}}
																InputLabelProps={{
																	shrink: true,
																}}
																style={{ marginTop: 8, maxWidth: 160 }}
															>
																{pages.map(option => (
																	<option key={option.value} value={option.value}>
																		{option.label}
																	</option>
																))}
															</TextField>
														)}
														</Field>
														<IconButton onClick={() => arrayHelpers.remove(index)} aria-label="Delete">
															<DeleteIcon />
														</IconButton>
													</div>
												))}
												<div className="text-center" style={{ marginTop: '16px'}}>
													<Button
														onClick={() => arrayHelpers.push({ text: '', id: pages[0].value })}
														color="secondary"
													>
														<i className="material-icons" style={{ marginRight: '4px'}}>add</i>
														
													</Button>
												</div>
											</div>
										)}
									/>
									<TextField
										name="fallback"
										value={values.fallback}
										label=""
										margin="normal"
										variant="filled"
										onChange={handleChange}
										onBlur={handleBlur}
										style={{width: "100%", marginTop: '36px'}}
									/>
								</Form>
							)}
						</Formik>
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color="primary">
							
            </Button>
            <Button onClick={this.handleSubmit} color="primary">
							
            </Button>
          </DialogActions>
			</Dialog>
		)
	}
}

const mapStateToProps = state => {
	return {
		editor: state.editor
	}
}

export default connect(mapStateToProps)(MenuEditor);
